import * as React from 'react';
import * as classNames from 'classnames';

/**
 * @enum
 * {
 *   不可见，
 *   可见，
 *   逐渐出现，
 *   逐渐消失，
 *   向左位移，
 *   向右位移，
 *   模糊,
 *   平移
 * }
 */
export const HEAP_MODE = {
  INVISIBLE: '',
  VISIBLE: 'visible',
  APPEAR: 'appear',
  DISAPPEAR: 'disappear',
  TOLEFT: 'to-left',
  TORIGHT: 'to-right',
  TOMID: 'to-mid',
  BLUR: 'blur',
  MOVE: 'move'
};

export interface HeapProps {
  heapModes?: Array<string>;
  style?: Object;
  className?: string;
};

interface HeapState {};

class Heap extends React.Component<HeapProps, HeapState> {
  static defaultProps = {
    heapModes: '',
    style: {}
  };

  constructor(props: HeapProps) {
    super(props);
  }

  render() {
    const { heapModes, style, className } = this.props;

    return (
      <div style={ style } className={ classNames('card-heap', heapModes, className)}>
      </div>
    );
  }
}

export default Heap;


