import * as React from 'react';
import { Link } from 'react-router';
import CommunityItem from '../../../components/CommunityItem';
import * as communityActions from '../../../actions/community';
import './recommandCommunity.scss';

interface RecommandCommunityProps {
  dispatch;
  community;
  profile;
  pagination;
  router;
}

interface RecommandCommunityState {}

export class RecommandCommunity extends React.Component<RecommandCommunityProps, RecommandCommunityState> {
  constructor(props: RecommandCommunityProps) {
    super(props);
  }

  transitionToResult(item) {
    const { router } = this.props;
    router.push(`/result/community/${item.question_id}?isOthers=true`);
  }

  async componentDidMount() {
    this.props.dispatch(communityActions.getRecommendList(this.props.profile.session, this.props.pagination, () => {}));
  }

  render() {
    const { community } = this.props;
    return (
      <div className="recommand-commumity">
        <header />
        <div className="community-list">
          {
            community.map((item, index) => {
              return <CommunityItem key={index} {...item} onClick={this.transitionToResult.bind(this, item)} />;
            })
          }
        </div>
        <Link to="community">
          <footer className="community-link">
            <i className="community-icon left" />
            <p>进入列表</p>
            <i className="community-icon right" />
          </footer>
        </Link>
      </div>
    );
  }
}



