import { GET_COMMUNITY_LIST, GET_COMMUNITY_RECOMMAND_LIST, CLEAR_COMMUNITY_LIST } from '../constants/actionType';
import { createReducer } from '../utils/createReducer';
import { TAROT } from '../constants/constant';
const Base64 = require('js-base64').Base64;

const initialState = {
  isEnd: false,
  pagination: {
    list: {
      page: -1,
      count: 7
    },
    recommand: {
      page: 0,
      count: 2 
    }
  },
  list: [],
  recommand: []
};

const TARTOT_MAP = {};
TAROT.map((t) => {
  TARTOT_MAP[t.name] = t.count;
});


const QUESTION_TYPE_MAP = {
  "事业": 0,
  "爱情": 1,
  "健康": 2,
  "财运": 3,
  "运势": 4,
  "人际": 5
};

function communityFormatter(question, face) {
  const detail = question.split('#');
  return {
    user: {
      nickname: detail[1],
      face_url: Base64.decode(face)
    },
    question_id: detail[0],
    question: detail[2],
    questionType: QUESTION_TYPE_MAP[detail[4]],
    questionTypeText: detail[4],
    storyDate: detail[5],
    cardCount: TARTOT_MAP[detail[3]]
  };
}

export default createReducer(initialState, {
  [GET_COMMUNITY_LIST.SUCCESS]: (state, payload) => {
    const list = payload.list.question_list.map((question, index) => {
      return communityFormatter(question, payload.list.question_face_list[index]);
    });
    const pagination = { ...state.pagination };
    pagination.list = payload.pagination;
    return {
      ...state,
      list: state.list.concat(list),
      pagination,
      isEnd: pagination.list.count > list.length
    };
  },
  [CLEAR_COMMUNITY_LIST.SUCCESS]: (state) => {
    return {
      ...state,
      ...initialState
    };
  },
  [GET_COMMUNITY_RECOMMAND_LIST.SUCCESS]: (state, payload) => {
    const recommand = payload.recommand.question_list.map((question, index) => {
      return communityFormatter(question, payload.recommand.question_face_list[index]);
    });
    return {
      ...state,
      recommand
    };
  }
});



