import * as React from 'react';
import * as ReactDOM from 'react-dom';
import classNames from 'classnames';

class Point {
  x: number
  y: number
  r: number
  sx: number
  sy: number

  constructor(x: number, y: number, sx?: number, sy?: number) {
    this.x = x;
    this.y = y;
    this.r = Math.random() * 2;
    this.sx = sx || Math.random() * 2 - 1;
    this.sy = sy || Math.random() * 2 - 1;
  }

  draw(ctx) {
    const { x, y, sx, sy, r } = this;
    const dx = x - 60 * sx;
    const dy = y - 60 * sy;
    const d = Math.sqrt(dx * dx + dy * dy);
    const alpha = (100 - d) / 100 * 1;

    const gntLine = ctx.createLinearGradient(x, y, dx, dy);
    gntLine.addColorStop(0, 'rgba(256, 256 ,256, 1)');
    gntLine.addColorStop(0.2, 'rgba(256, 256 ,256, 0.4)');
    gntLine.addColorStop(0.7, 'rgba(256, 256 ,256, 0)');

    ctx.beginPath();
    ctx.moveTo(x, y);
    ctx.lineTo(dx, dy);
    ctx.strokeWidth = 0.1; 
    ctx.strokeStyle = gntLine;
    ctx.lineJoin = 'miter';
    ctx.closePath();
    ctx.stroke();
  }

  isOutOfBorder() {
    const { x, y, sx, sy, r } = this;
    const dx = x - 60 * sx;
    const dy = y - 60 * sy;

    return dx < 0 || dy < 0;
  }

  move({ height, width }) {
    this.x += this.sx;
    this.y += this.sy;

    if(this.isOutOfBorder()) {
      this.x = width;
      this.y = height * Math.random();
    }
  }
}

interface ParticleProps {
  className?: string,
  style?: Object
};

class Particle extends React.Component<ParticleProps, any> {
  private rAF;

  constructor(props: ParticleProps) {
    super(props);
  }

  componentDidMount() {
    const canv: any =  this.refs.canvas;
    // const canv = ReactDOM.findDOMNode(canvas);
    const ctx = canv.getContext('2d');

    const height = canv.offsetHeight;
    const width = canv.offsetWidth;

    /**
     * @desc
     * Q: 此处为什么要让canvas的height和width等于offsetHeight和width?
     **/
    canv.width = width;
    canv.height = height;

    const points = [];

    for(let i = 0; i < Math.floor(Math.random() * 10) + 5; i++) {
      points.push(new Point(Math.random() * width, Math.random() * height, -Math.random(), Math.random() / 2 +  0.25));
    }

    this.loop({ ctx, points, width, height });
  }

  componentWillUnmount() {
    if(this.rAF) {
      cancelAnimationFrame(this.rAF);
      this.rAF = null;
    }
  }

  loop = ({ ctx, points, width, height }) => {
    /**
     * @desc
     * Q: requestAnimationFrame为什么不放在paint函数中?
     **/
    this.paint(ctx, points, width, height);

    this.rAF = requestAnimationFrame(() => this.loop({ ctx, points, width, height }));
  }

  paint = (ctx, points, width, height) => {
    ctx.clearRect(0, 0, width, height);

    // points = points.filter((point) => !point.isOutOfBorder());

    points.forEach((point) => {
      point.move({ height, width });

      point.draw(ctx);
    });
  }

  render() {
    const { className, style } = this.props;

    return (
      <canvas style={ style } className={ className } ref="canvas" id="canvas">
      </canvas>
    );
  }
}

export default Particle;


