import { HISTORY, SIGN_IN } from '../constants/actionType';

export function signIn(session, callback) {
  return {
    type: SIGN_IN.ACTION,
    payload: {
      session,
      callback
    }
  };
}

export function getHistory(session, callback) {
  return {
    type: HISTORY.ACTION,
    payload: {
      session,
      callback
    }
  };
}



