import { SET_NAV_CONFIG, SET_BACKGROUND, SET_SHARE_CONTENT } from '../constants/actionType';
import { SHARE_CONTENT } from '../constants/constant';
import { createReducer } from '../utils/createReducer';

const initialState = {
  navConfig: null,
  shareContent: {
    icon: SHARE_CONTENT.icon,
    text: '',
    title: '你今天走运吗?',
    link: SHARE_CONTENT.url,
    channel: 'wechat',
    comment: '',
    type: 'webpage' /**@desc type can be image/webpage/text */
  }
};

export default createReducer(initialState, {
  [SET_NAV_CONFIG]: (state, payload) => {
    const { config: navConfig } = payload;

    return {
      ...state,
      navConfig
    };
  },
  [SET_BACKGROUND]: (state, payload) => {
    const { bg } = payload;

    return {
      ...state,
      bg
    };
  },
  [SET_SHARE_CONTENT]: (state, payload) => {
    const { content: shareContent } = payload;

    return {
      ...state,
      shareContent: {
        ...state.shareContent,
        ...shareContent
      }
    };
  }
});



