import * as React from 'react';

interface LoginBtnProps {
  text: string,
  type: string,
  onClick?: any
}

interface LoginBtnState {}

class LoginBtn extends React.Component<LoginBtnProps, LoginBtnState> {
  constructor(props: LoginBtnProps) {
    super(props);
  }

  render() {
    const { text, type, onClick } = this.props;

    return (    
      <div className="btn-login-wrapper">
        <a
          onClick={ onClick }
          className={ `btn-login ${type}` }
          />

        <div className="text">
          { text }
        </div>
      </div>
    );
  }
} 

export default LoginBtn;


