import * as React from 'react';
import * as _ from 'lodash';
import { Router, Route, IndexRedirect } from 'react-router';
import { useBasename } from 'history';
const queryString = require('query-string');

import * as UserActions from './actions/user';
import UserService from './services/user';
import { setNavConfig, setBackground } from './actions/global';
import { ROUTE_ROOT } from './constants/constant';

import App from './containers/App';
import Login from './containers/Login';
import Today from './containers/Today';
import HoroscopeSelector from './horoscope-selector/HoroscopeSelector';
import Shuffle from './containers/Shuffle';
import Result from './containers/Result';
import ModeSelect from './containers/ModeSelect';
import Formation from './containers/Formation';
import QuestionType from './containers/QuestionType';
import QuestionInput from './containers/QuestionInput';
import Community from './containers/Community';
import Newbie from './containers/Newbie';
import NotFound from './containers/NotFound';
import AccountProfile from './containers/Account/AccountProfile';
import AccountAbout from './containers/Account/AccountAbout';
import AccountNickname from './containers/Account/AccountNickname';
import AccountBirthday from './containers/Account/AccountBirthday';
import AccountAddress from './containers/Account/AccountAddress';

interface RoutesProps {
  history;
  store;
}
interface RoutesState { }
class Routes extends React.Component<RoutesProps, RoutesState> {
  routeConfig = [
    {
      path: 'today',
      component: Today,
      nav: null,
      bg: 'today'
    },
    {
      path: 'horoscope-selector',
      component: HoroscopeSelector,
      nav: { title: '选择星座', left: { type: 'back' } },
      bg: 'login'
    },
    {
      path: 'shuffle',
      component: Shuffle,
      nav: { title: '塔罗占卜', left: { type: 'back' } },
      bg: 'divine'
    },
    {
      path: 'result/story/:id',
      component: Result,
      nav: {
        title: '塔罗占卜',
        left: { type: 'home' },
        right: { type: 'share' },
        fixed: true
      },
      bg: 'result'
    },
    {
      path: 'result/today/:cardId',
      component: Result,
      nav: {
        title: '今日运势',
        left: { type: 'back' },
        right: { type: 'share' },
        fixed: true
      },
      bg: 'result'
    },
    {
      path: 'result/daily/:cardId(/:date)',
      component: Result,
      nav: ({ date }) => ({
        title: date,
        left: { type: 'back' },
        right: { type: 'share' },
        fixed: true
      }),
      bg: 'result'
    },
    {
      path: 'result/community/:id',
      component: Result,
      nav: {
        title: '塔罗占卜',
        left: { type: 'back' },
        right: { type: 'share' },
        fixed: true
      },
      bg: 'result'
    },
    {
      path: 'mode/select',
      component: ModeSelect,
      nav: { title: '选择模式', left: { type: 'back' } },
      bg: 'divine'
    },
    {
      path: 'formation',
      component: Formation,
      nav: { title: '选择阵型', left: { type: 'back' } },
      bg: 'divine'
    },
    {
      path: 'question/type(/:mode)',
      component: QuestionType,
      nav: { title: '选择类型', left: { type: 'back' } },
      bg: 'divine'
    },
    {
      path: 'question/input(/:mode)',
      component: QuestionInput,
      nav: { title: '输入问题', left: { type: 'back' } },
      bg: 'divine'
    },
    {
      path: 'community(/:self)',
      component: Community,
      nav: ({ self }) => {
        return self
          && { title: '我的占卜', left: { type: 'back' } }
          || { title: '塔罗墙', left: { type: 'back' } }
          ;
      },
      bg: 'community'
    },
    {
      path: 'newbie',
      component: Newbie,
      nav: { title: '选择牌的张数', left: { type: 'back' } },
      bg: 'divine'
    },
    {
      path: 'account',
      component: AccountProfile,
      nav: { title: '个人中心', left: { type: 'back' } },
      bg: 'account'
    },
    {
      path: 'account/about',
      component: AccountAbout,
      nav: { title: '关于我们', left: { type: 'back' } },
      bg: 'account'
    },
    {
      path: 'account/nickname(/:isNew)',
      component: AccountNickname,
      nav: ({ isNew }) => {
        return isNew
          && { title: '昵称', left: null }
          || { title: '昵称', left: { type: 'back' } }
          ;
      },
      bg: 'account'
    },
    {
      path: 'account/birthday',
      component: AccountBirthday,
      nav: { title: '生日', left: { type: 'back' } },
      bg: 'account'
    },
    {
      path: 'account/address',
      component: AccountAddress,
      nav: { title: '收货地址', left: { type: 'back' } },
      bg: 'account'
    },
  ];

  renderRoute = (config, dispatch, key?) => {
    const {
      path,
      component,
      nav,
      bg,
      children
    } = config;

    const onRouteEnter = ({ params }) => {
      /** set navigation */
      (_.isFunction(nav)) &&
        dispatch(setNavConfig(nav(params))) ||
        dispatch(setNavConfig(nav))
        ;

      dispatch(setBackground(bg));
    };

    return (
      <Route
        key={key}
        path={path}
        component={component}
        onEnter={onRouteEnter}
      >
        {children && this.renderRoute(children, dispatch)}
      </Route>
    )
  }

  /**
   * @desc checkLogin的过程:
   * if profile.session 
   *  callback
   * else if localstorage中存在session
   *   获取session和isnewplayer信息
   *   检查session是否过期
   *   if session过期:
   *     跳转到登陆页面
   *   else:
   *     更新session，并调用callback
   * else if query中存在session
   *   将session和isnewplayer保存到localstorage中
   *   callback
   * else
   *   跳转到登陆页面 */
  onEnter = (nextState, replaceState, callback, store) => {
    const profile = store.getState().profile;
    const query = queryString.parse(window.location.search);
    const { history } = this.props;

    const checkLoginAndUpdate = (session, isNew, cb) => {
      UserService.checkLogin(session).then(({ session }) => {
        if (!session || session === 'nil') {
          history.push(`${ROUTE_ROOT}login`);
        } else {
          localStorage.setItem('TAROT_SESSION', session);
          store.dispatch(UserActions.getSelfProfile(session, isNew, cb));
        }
      });
    };
    callback();

    // if (profile.session) {
    //   callback();
    // } else if (localStorage.getItem('TAROT_SESSION')) {
    //   checkLoginAndUpdate(
    //     localStorage.getItem('TAROT_SESSION'),
    //     0,
    //     callback
    //   );
    // } else if(query.session) {
    //   const { session, isnewplayer } = query;

    //   checkLoginAndUpdate(session, isnewplayer, callback);
    // } else {
    //   history.push(`${ROUTE_ROOT}login`);
    // }
    // callback();
  }

  /**
   * @desc 判断用户是否是新用户
   * 当用户存在profile, query或localstorage之一时
   * 优先级为
   * reducer.profile > localstorage IS_NEW > query isnewplayer
   * if isNew = false then it's not a new user;
   */
  isNotNew = () => {
    const isNot = (value) => value === false || value == 0 || value === 'fasle';

    const { store } = this.props;
    const profile = store.getState().profile;
    const query = queryString.parse(window.location.search);
    const queryIsNew = query.isnewplayer;
    const lsIsNew = localStorage.getItem('IS_NEW');

    return !queryIsNew || (isNot(profile.isNew) || isNot(lsIsNew) || isNot(queryIsNew));
  }

  render() {
    const { routeConfig } = this;
    const { store } = this.props;
    const profile = store.getState().profile;

    const indexRedirectTo = this.isNotNew() && 'today' || 'account/nickname/new';

    return (
      <Router history={useBasename(() => this.props.history)({ basename: ROUTE_ROOT })}>
        <Route path="/login" component={Login} />

        <Route path="/" component={App} onEnter={(nextState, replaceState, callback) => this.onEnter(nextState, replaceState, callback, this.props.store)}>
          <IndexRedirect to={indexRedirectTo} />
          {
            this.routeConfig.map((config, index) => this.renderRoute(config, store.dispatch, index))
          }
          <Route path="*" component={NotFound} />
        </Route>
      </Router>
    );
  }
}

export default Routes;



