import * as React from 'react';
import delay from '../../../utils/delay';

import './todayDetailTip.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示
export enum todayDetailTipStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE
}

interface TodayDetailTipProps {
  text: string;
  status: todayDetailTipStatus;
}

interface TodayDetailTipState {
  status: todayDetailTipStatus;
}

export class TodayDetailTip extends React.Component<TodayDetailTipProps, TodayDetailTipState> {
  constructor(props: TodayDetailTipProps) {
    super(props);
    this.state = {
      status: todayDetailTipStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  async componentDidMount() {
    this.changeStatus([0, todayDetailTipStatus.MOUNTED]);
  }

  getClassByStatus(status) {
    return status !== todayDetailTipStatus.HIDDEN && status !== todayDetailTipStatus.MOUNTED ? 'visible' : '';
  }

  render() {
    const { text } = this.props;
    const { status } = this.state;
    if (status === todayDetailTipStatus.HIDDEN) {
      return null;
    }
    return (
      <div className={`today-detail-tip ${this.getClassByStatus(status)}`}>
        <p dangerouslySetInnerHTML={{__html: text}} />
      </div>
    );
  }
}



