import * as React from 'react';
import * as classNames from 'classnames';
import { connect } from 'react-redux';

import Preload from '../../components/Preload';
import Nav from '../../components/Nav';
import Loading from './preloading';
import seqSetState from '../Shuffle/seqSetState';
import CommonService from '../../services/common';
import { PRELOAD_IMGS } from '../../constants/constant';

import './app.scss';
// 真机调试log
// import 'vconsole';

interface AppProps {
  router: any;
  location: any;
  profile: any;
  navConfig: any;
  bg: any;
  shareContent: any;
}

class App extends React.Component<AppProps, any> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      percent: 0
    };
  }

  componentDidMount() {
    window.addEventListener('click', this.handleClick, true);
  }

  currPercent = () => {
    return this.state.percent;
  }

  onPreloadingSuccess = () => {
    this.setState ({
      percent: 100
    });
  }

  preloadConfig = () => {
    return {
      images: PRELOAD_IMGS,
      loadingIndicator: <Loading percent={ this.state.percent } />,
      onSuccess: this.onPreloadingSuccess,
      getProgress: (curr, total) => {
        this.setState({
          percent: Math.round(curr / total * 100)
        });
      }
    }
  };

  handleClick = (e) => {
    // if (Object.keys(e.target.dataset).length) {
    //   const page = this.props.location.pathname.substr(1);
    //   const { nickname, openid } = this.props.profile.selfProfile;
    //   const { action } = e.target.dataset;
    //   const params = { command: 1011, trace_action: `h5_${action}`, ip: `${openid}_${nickname}`, data1: `page_${page}`, };

    //   CommonService.postTrackPoint(params);
    // }
  }

  render() {
    const { children, navConfig, bg, router, shareContent } = this.props;

    return (
      <Preload {...this.preloadConfig()}>
        <div className="app">
          <Nav
            visible={ !!navConfig }
            {...navConfig}
            router={ router }
            shareContent={ shareContent }
            />
          <div className={ 
            classNames([
              "main-wrapper",
              {"has-nav": !!navConfig},
              bg && `bg-${bg}` || 'bg-login'
            ])}
            >
            { children }
          </div>
        </div>
      </Preload>
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    navConfig: state.global.navConfig,
    bg: state.global.bg,
    shareContent: state.global.shareContent
  };
};

export default connect(mapStateToProps, {})(App);



