import * as React from 'react';
import * as classNames from 'classnames';

import seqSetState from '../seqSetState';
import delay from '../../../utils/delay';

import { toast } from '../../../components/Notification';
import Card, { CARD_MODE } from './card';
import { TodayShine as Shine, todayShineStatus } from '../../Today/TodayShine';
import Button from '../../../components/Button';

import './cardSelection.scss';

export interface CardSelectionProps {
  maxSelectNum: Number;
  onSelected: Function;
  animEndAction: Function;
};

const TOTAL_CARDS = 21;

class CardSelection extends React.Component<CardSelectionProps, any> {
  static defaultProps = {
    maxSelectNum: 3
  };

  private cardRefs:Array<Card> = [];

  private cardInitAnim = (index) => ([{ duration: 100 }, { duration: 0, states: [{ [`cardsMode${index}`]: [CARD_MODE.APPEAR] }] }]);
  private cardEndAnim = (index, isSelected, selectedIdx) => ([{ duration: 0, states: [{ [`cardsMode${index}`]: isSelected && [CARD_MODE.TOHEAP[selectedIdx]] || [CARD_MODE.DISAPPEAR] }] } ]);
  private cardBoardEndAnim = { duration: 0, states: [{ showBoard: false }] };

  constructor(props: CardSelectionProps) {
    super(props);

    this.state = {
      selected: [],
      allowOperation: false,
      shineMode: todayShineStatus.MOUNTED,
      showBoard: true
    };
  }

  private drawBoard = () => {
    const { allowOperation, selected } = this.state;
    const cards = [];

    for(let i = 0; i < TOTAL_CARDS; i++) {
      cards.push(
        <Card
          key={ i }
          className={ classNames(['on-board', { 'selected': selected.indexOf(i) > -1 }]) }
          mode={ this.state[`cardsMode${i}`]}
          ref={(card) => { this.cardRefs[i] = card; }}
          onClick={ () => this.onCardSelect(allowOperation)(i) }
          />
      );
    }

    return cards;
  }

  private onCardSelect = (canExec) => (index) => {
    const { selected } = this.state;
    const { maxSelectNum } = this.props;

    if(!canExec) return ;

    const hasIndex = selected.indexOf(index) > -1;

    this.setState({
      selected: (hasIndex
        && selected.filter((i) => i !== index))
        || (selected.length < maxSelectNum && [...selected, index]) || selected
    });
  }

  private onConfirm = (canExec) => async () => {
    const { maxSelectNum, onSelected } = this.props;
    const { selected } = this.state;

    if(!canExec || selected.length < maxSelectNum) {
      toast(`请选择${maxSelectNum}张牌`);
      return ;
    }

    selected.length === maxSelectNum && await this.playEnd();

    onSelected && onSelected();
  }

  public playInit = async () => {
    let cardInitAnims = [];

    for(let i = 0; i < TOTAL_CARDS; i++) {
      cardInitAnims = cardInitAnims.concat(this.cardInitAnim(i));
    }

    await seqSetState(cardInitAnims, (states) => this.setState(states));

    this.setState({
      allowOperation: true
    });
  }

  public playEnd = async () => {
    const { selected } = this.state;
    const { cardRefs } = this;
    const { animEndAction } = this.props;

    selected.forEach((idx) => {
      cardRefs[idx].sticky(idx * 10);
    });

    let endAnims = [];

    for(let i = 0; i < TOTAL_CARDS; i++) {
      endAnims = endAnims.concat(this.cardEndAnim(i, selected.indexOf(i) > -1, selected.indexOf(i)));
    }

    endAnims = endAnims.concat(this.cardBoardEndAnim);

    const shineDuration = (content) => async ()=> {
      const minDuration = 3000;
      const startAt = new Date().getTime();
      await animEndAction(content);
      const endAt = new Date().getTime();
      if(endAt - startAt < minDuration) {
        await delay(minDuration - (endAt - startAt));
      }
    };

    /**
     * @todo
     * 动画的duration应该是动态的，取决于抽牌接口的返回
     */
    endAnims = endAnims.concat([{ duration: 600 }, { duration: shineDuration(selected), states: [{ shineMode: todayShineStatus.VISIBLE }] } ]);

    this.setState({
      allowOperation: false
    });

    await seqSetState(endAnims, (states) => this.setState(states));
  }

  render() {
    const { maxSelectNum } = this.props;
    const { selected, allowOperation, shineMode, showBoard } = this.state;

    return (
      <div className={ "card-selection-wrapper" }>

        <div className={ classNames(["card-board", { disappear: !showBoard }]) }>
          <div className={ classNames(["select-notify", { visible: !!allowOperation }]) }>
            请选择
            <span className="num">{ maxSelectNum }</span>
            张牌
          </div>
          { this.drawBoard() }
        </div>

        <div className={ classNames(["btn-wrapper", { visible: !!allowOperation }]) }>
          <span className="selected-notify">
            已经选择了
            <span className="num">
              { selected.length }
            </span>
            张牌
          </span>
          <div className="btn-select">
            <Button
              onClick={ this.onConfirm(allowOperation) }
              text="立即开牌"
              size="small"
              theme="green"
              />
          </div>
        </div>

        <Shine
          style={{ position: 'fixed', top: '0', zIndex: -1 }}
          status={ shineMode }
          />
      </div>
    );
  }
}

export default CardSelection;



