/**
 * 运势日历，单项
 * @since 2017-09-29
 * @author yuan.gao@dianrong.com
 */

import * as React from 'react';
const moment = require('moment') ;

import CalendarItem from './CalendarItem';

import './calendar.scss';

export enum calendarMode {
  SINGLE,
  FULL,
  BOTH_SINGLE,
  BOTH_FULL
}

interface CalendarProps {
  mode: calendarMode;
  tarots;
  onItemClick?;
}

moment.locale('week-setting', {
  week : {
      dow : 1 // Monday is the first day of the week.
  }
});

export class Calendar extends React.Component<CalendarProps, {}> {
  constructor(props: CalendarProps) {
    super(props);
  }

  renderSingleMode() {
    const firstDayOfWeek = moment().startOf('week');

    const todayDate = moment().date();
    const items = [];
    const { tarots, onItemClick } = this.props;

    for (let i = 0; i < 7; i++) {
      items.push(
        <CalendarItem
          key={i}
          date={firstDayOfWeek.format('D')}
          highlight={todayDate === firstDayOfWeek.date()}
          tarot={tarots[firstDayOfWeek.format('YYYY-MM-DD')] || ''}
          onClick={ onItemClick }
        />
      );
      firstDayOfWeek.add(1, 'days');
    }
    return (
      <div className="calendar-column calendar-content-single">
        { items }
      </div>
    );
  }

  renderFullMode() {
    const todayDate = moment().date();
    const firstDay = moment().startOf('month');
    const firstDayDay = firstDay.day() || 7;
    const lastDay = moment().endOf('month');
    const tarots = this.props.tarots;
    const items = [];
    const firstDayOfCalendar = moment().startOf('month').add(1 - firstDayDay, 'days');
    const { onItemClick } = this.props;
    // 补位
    for (let i = 0; i < firstDayDay - 1; i++) {
      items.push(
        <CalendarItem
          onClick={ onItemClick }
          key={i}
          date={firstDayOfCalendar.format('D')}
          dark={true}
        />
      );
      firstDayOfCalendar.add(1, 'days');
    }

    while (firstDay <= lastDay) {
      items.push(
        <CalendarItem
          onClick={ onItemClick }
          key={firstDay.format('x')}
          dark={todayDate < firstDay.date()}
          date={firstDay.format('D')}
          highlight={todayDate === firstDay.date()}
          tarot={tarots[firstDay.format('YYYY-MM-DD')] || ''}
          />
      );
      firstDay.add(1, 'days');
    }

    return (
      <div className="calendar-column calendar-content-full">
        { items }
      </div>
    );
  }

  render() {
    const { mode } = this.props;
    return (
      <div className={`calendar ${mode === calendarMode.BOTH_SINGLE ? 'both single' : ''} ${mode === calendarMode.BOTH_FULL ? 'both full' : ''}`}>
        <div className="calendar-column">
          <div className="calendar-week">MON</div>
          <div className="calendar-week">TUS</div>
          <div className="calendar-week">WED</div>
          <div className="calendar-week">THU</div>
          <div className="calendar-week">FRI</div>
          <div className="calendar-week"><b>SAT</b></div>
          <div className="calendar-week"><b>SUN</b></div>
        </div>
        { mode === calendarMode.SINGLE || mode === calendarMode.BOTH_SINGLE ? this.renderSingleMode() : null }
        { mode === calendarMode.FULL || mode === calendarMode.BOTH_FULL ? this.renderFullMode() : null }
      </div>
    );
  }
}



