import * as decoder from 'js-base64';
import { takeEvery, takeLatest, put, call, fork } from 'redux-saga/effects';

import {
  STORY_MODE_SELECT,
  STORY_QUESTION_TYPE_SELECT,
  STORY_QUESTION_INPUT,
  STORY_FORMATION_SELECT,
  STORY_PRIVATE,
  STORY_GET_RESULT,
  GET_DAILY_DIVINE,
  GET_RESULT_BY_ID,
} from '../constants/actionType';
import StoryService from '../services/story';

const formatResultCardInfo = (card) => {
  /**
   * @desc
   * cardInfo[7]如果存在，则表明这是一个是与否问题
   */
  const cardInfo = card.split('#');
  return {
    index: cardInfo[2],
    name: cardInfo[3],
    direction: cardInfo[4],
    meaning: cardInfo[1],
    yesOrNo: cardInfo[7] ? (
      cardInfo[6] === '是' ? true : (cardInfo[6] === '否' ? false : null)
    ) : null,
    content: [
      {
        title: cardInfo[3],
        content: cardInfo[5]
      },
      {
        title: '牌面详解',
        decorate: true,
        content: cardInfo[7] || cardInfo[6] //是与否问题时取cardInfo[7]
      }
    ]
  };
}

export function* storyModeSelect(action) {
  try {
    yield put({
      type: STORY_MODE_SELECT.SUCCESS,
      payload: {
        mode: action.payload.mode
      }
    });
    if (action.payload.callback) {
      action.payload.callback();
    }
  } catch (err) {
    yield put({ type: STORY_MODE_SELECT.FAILED });
  }
}

