/**
 * @deprecated use storyResultById
 */
export function* storyResult(action) {
  const {
    story,
    session,
    callback
  } = action.payload;

  const result = yield call(StoryService.storyResult, story, session);

  const question = yield call(StoryService.storyResultById, result.question_id, session);

  try {
      yield put({
        type: GET_RESULT_BY_ID.SUCCESS,
        payload: {
          ...question,
          ...action.payload
        }
    });
    callback && callback(result);
  } catch(err) {
    yield put({ type: STORY_GET_RESULT.FAILED });
  }
}

export function* dailyDivine(action) {
  const {
    card,
    session,
    callback
  } = action.payload;
  try {
    const result = yield call(StoryService.dailyDivine, card, session);

    const cardsInfos = [formatResultCardInfo(`##${card}#` + result.card_infos.split('#').slice(1).join('#'))];

    yield [
      put({
        type: GET_DAILY_DIVINE.SUCCESS,
        payload: {
          cardsInfos,
          card
        }
      }),
      // put({
      //   type: SET_SHARE_CONTENT,
      //   payload: {
      //     content: {
      //       title: '每日运势',
      //       text: '我的运势ok!',
      //       link: `http://${window.location.host}`,
      //       icon: `http://app.magichouse.cc/i/tarot-1.png`,
      //       comment: `塔罗测算`,
      //       channel: 'wechat'
      //     }
      //   }
      // })
    ];

    callback && callback(result);
  } catch(err) {
    yield put({ type: GET_DAILY_DIVINE.FAILED });
  }
}

export function* storyResultById(action) {
  const {
    id,
    session,
    callback
  } = action.payload;

  const result = yield call(StoryService.storyResultById, id, session);

  const { tarot_infos } = result;

  try {

    const tarotInfo = JSON.parse(decoder.Base64.decode(tarot_infos));
    const cardsInfos = tarotInfo.cards_infos_array && tarotInfo.cards_infos_array.map(formatResultCardInfo) || [];

    yield [
      put({
        type: GET_RESULT_BY_ID.SUCCESS,
        payload: {
          // ...result,
          ...action.payload,
          ...tarotInfo,
          cardsInfos
        }
      }),
      // put({
      //   type: SET_SHARE_CONTENT,
      //   payload: {
      //     content: {
      //       title: '塔罗测算',
      //       text: '我测算了一下!',
      //       link: `http://${window.location.host}`,
      //       icon: `http://app.magichouse.cc/i/tarot-1.png`,
      //       comment: `塔罗测算`,
      //       channel: 'wechat'
      //     }
      //   }
      // })
    ];

    if (callback) {
      callback(result);
    }
  } catch (err) {
    yield put({ type: GET_RESULT_BY_ID.FAILED });
  }
}

export function* watchStoryModeSelect() {
  yield takeEvery(STORY_MODE_SELECT.ACTION, storyModeSelect);
}

export function* watchStoryQuestionTypeSelect() {
  yield takeEvery(STORY_QUESTION_TYPE_SELECT.ACTION, storyQuestionTypeSelect);
}

export function* watchStoryQuestionInput() {
  yield takeEvery(STORY_QUESTION_INPUT.ACTION, storyQuestionInput);
}

export function* watchStoryFormationSelect() {
  yield takeEvery(STORY_FORMATION_SELECT.ACTION, storyFormationSelect);
}

export function* watchStoryPrivate() {
  yield takeEvery(STORY_PRIVATE.ACTION, storyPrivate);
}

export function* watchStoryResult() {
  yield takeLatest(STORY_GET_RESULT.ACTION, storyResult);
}

export function* watchDailyDivine() {
  yield takeLatest(GET_DAILY_DIVINE.ACTION, dailyDivine);
}

export function* watchStoryResultById() {
  yield takeLatest(GET_RESULT_BY_ID.ACTION, storyResultById);
}

export default function* story() {
  yield fork(watchStoryModeSelect);
  yield fork(watchStoryQuestionTypeSelect);
  yield fork(watchStoryQuestionInput);
  yield fork(watchStoryFormationSelect);
  yield fork(watchStoryPrivate);
  yield fork(watchStoryResult);
  yield fork(watchDailyDivine);
  yield fork(watchStoryResultById);
}



