import * as React from 'react';

class Wave {
  frames = 0;
  freq;
  ampl;
  requestId;
  height;
  width;
  ctx;
  offset=50;

  constructor(ctx, freq, ampl, height, width) {
    this.freq = freq || .03;
    this.ampl = ampl || 30;
    this.height = height || 400;
    this.width = width || 250;
    this.ctx = ctx;

    this.draw = this.draw.bind(this);
  }

  start() {
    this.requestId = window.requestAnimationFrame(this.draw);
  }

  stop() {
    this.requestId = null;
  }

  draw() {
    const { offset, freq, ampl, height, width, ctx } = this;

    this.frames++;

    const phi = this.frames / 30;

    ctx.lineWidth = 2;
    ctx.clearRect(0, 0, height, width);

    for(let x = 0; x < width; x ++) {
      const y = height / 2 + Math.sin(x * freq + phi) * ampl; 

      ctx.strokeStyle = 'rgba(76, 174, 231, 1)';
      ctx.beginPath();
      ctx.moveTo(x, y);
      ctx.lineTo(x, y + 500);
      ctx.stroke();

      ctx.strokeStyle = 'rgba(3, 103, 197, 0.7)';
      ctx.beginPath();
      ctx.moveTo(x + offset, height / 2 - 1);
      ctx.lineTo(x, y + 500);
      ctx.stroke();

      // ctx.strokeStyle = 'rgb(76, 174, 231)';
      // ctx.beginPath();
      // ctx.arc(x, y, 2, 0, 2 * Math.PI);
      // ctx.fill();

      // ctx.strokeStyle = 'rgb(3, 103, 197)';
      // ctx.beginPath();
      // ctx.arc(x + offset, y, 2, 0, 2 * Math.PI);
      // ctx.fill();
    }

    ctx.stroke();

    this.requestId = window.requestAnimationFrame(this.draw);
  }
}

interface LoadingProps {
  percent?: number;
}

interface  LoadingState {}

class Loading extends React.Component<LoadingProps, LoadingState> {
  waves: Wave;

  constructor(props: LoadingProps) {
    super(props);
  }

  componentDidMount() {
    const canv: any = this.refs.canvas;
    const ctx = canv.getContext('2d');
    const height = canv.offsetHeight;
    const width = canv.offsetWidth;

    canv.width = width;
    canv.height = height;

    this.waves = new Wave(ctx, 0.08, 3, height, width);

    this.waves.start();
  }

  componentWillUnmount() {
    this.waves.stop();
  }

  render() {
    const { percent } = this.props;

    return (
      <div className="preloading">
        <div className="banner" />
        <div className="title" />
        <div className="progress">
          <div className="p-icon">
            <div className="planet" />
            <div className="logo" />
            <canvas ref="canvas" />
          </div>
          <div className="p-indicator">
            { `${percent}%` }
          </div>
        </div>
      </div>
    );
  }
}

export default Loading;


