import {
  GET_SELF_PROFILE,
  UPDATE_SELF_PROFILE,
  SET_NEW_USER_STATE
} from '../constants/actionType';
import { createReducer } from '../utils/createReducer';
const Base64 = require('js-base64').Base64;

const initialState = {
  session: '',
  face_url: '',
  nickname: '',
  sex: null,
  birthday: '',
  isNew: null,
  coin: '',
  address: {
    name: '',
    tel: '',
    location: ''
  }
};

export default createReducer(initialState, {
  [GET_SELF_PROFILE.SUCCESS]: (state, payload) => {
    return {
      ...state,
      ...payload,
      face_url: Base64.decode(payload.face_url)
    };
  },
  [UPDATE_SELF_PROFILE.SUCCESS]: (state, payload) => {
    return {
      ...state,
      ...payload
    };
  },
  [SET_NEW_USER_STATE]: (state, payload) => {
    const { isNew } = payload;

    return {
      ...state,
      isNew
    };
  }
});



