import * as React from 'react';
import { connect } from 'react-redux';

import Preload from '../../components/Preload';
import LoginBtn from './LoginBtn';
import Loading from '../App/preloading';

import seqSetState from '../Shuffle/seqSetState';

import { LOGIN_REDIRECT_URL } from '../../constants/api';

import './login.scss';

const Base64 = require('js-base64').Base64;

interface LoginProps {
  params: Object;
}

interface LoginState {
  percent;
}

const LOGIN_IMGS = [
  'i/bg-login.png',
  'i/icon-wechat@2x.png',
  'i/icon-qq@2x.png',
  'i/icon-weibo@2x.png'
];

class Login extends React.Component<LoginProps, LoginState> {
  constructor(props: LoginProps) {
    super(props);

    this.state = {
      percent: 0
    };
  }

  componentDidMount() {
  }

  loginWith = (sns) => () => {
    const loginUrl = (url, type) => `UniWebView://auth?url=${url}&type=${type}`;
    /**
     * @todo
     * 根据不同的sns跳转到相应的授权登陆页
     */
    localStorage.removeItem('TAROT_SESSION');
    localStorage.removeItem('IS_NEW');

    window.location.href = loginUrl(Base64.encode(LOGIN_REDIRECT_URL), Base64.encode(sns).replace(/=/g, '$'));
  }

  btnGroup = [
    {
      type: 'wechat',
      text: '微信',
      onClick: this.loginWith('wechat')
    },
    {
      type: 'qq',
      text: 'QQ',
      onClick: this.loginWith('qq')
    },
    {
      type: 'weibo',
      text: '微博',
      onClick: this.loginWith('weibo')
    }
  ];

  onPreloadingSuccess = () => {
    this.setState ({
      percent: 100
    });
  }

  preloadConfig = () => {
    return {
      images: LOGIN_IMGS,
      loadingIndicator: <Loading percent={ this.state.percent } />,
      onSuccess: this.onPreloadingSuccess,
      getProgress: (curr, total) => {
        this.setState({
          percent: Math.round(curr / total * 100)
        });
      }
    };
  }

  render() {
    return (
      <Preload {...this.preloadConfig()}>
        <div className="login">
          <div className="title-wrapper">
            请选择登陆方式
          </div>

          <div className="btn-group">
            {
              this.btnGroup.map((btn, index) => {
                return (
                  <div className="item-wrapper" key={index}>
                    <LoginBtn { ...btn } />
                  </div>
                );
              })
            }
          </div>

          <div onClick={ this.loginWith('guest') } className="hint-wrapper">
            跳过，先玩一玩
          </div>
        </div>
      </Preload>
    );
  }
}

export default Login;



