import * as React from 'react';
import delay from '../../../utils/delay';

import './todayPlayTip.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示
export enum todayPlayTipStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE
}

interface TodayPlayTipProps {
  text: string;
  status: todayPlayTipStatus;
}

interface TodayPlayTipState {
  status: todayPlayTipStatus;
}

export class TodayPlayTip extends React.Component<TodayPlayTipProps, TodayPlayTipState> {
  constructor(props: TodayPlayTipProps) {
    super(props);
    this.state = {
      status: todayPlayTipStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  async componentDidMount() {
    this.changeStatus([0, todayPlayTipStatus.MOUNTED]);
  }

  getClassByStatus(status) {
    return status !== todayPlayTipStatus.HIDDEN && status !== todayPlayTipStatus.MOUNTED ? 'visible' : '';
  }

  render() {
    const { text } = this.props;
    const { status } = this.state;
    if (status === todayPlayTipStatus.HIDDEN) {
      return null;
    }
    return (
      <div className={`today-play-tip ${this.getClassByStatus(status)}`}>
        <p dangerouslySetInnerHTML={{__html: text}} />
      </div>
    );
  }
}



