import * as React from 'react';
import * as classNames from 'classnames';

import './formationItem.scss';

interface FormationProps {
  formation;
  onClick?: any;
  selected?: boolean;
  className?: string;
  size?: 'sm'|'mid';
}

class FormationItem extends React.Component<FormationProps, {}> {
  static defaultProps = {
    size: 'sm',
    formation: { id: 0 }
  };

  constructor(props: FormationProps) {
    super(props);
    this.click = this.click.bind(this);
  }

  click() {
    if (this.props.onClick) {
      this.props.onClick();
    }
  }

  render() {
    const { formation, selected, className, size } = this.props;

    return (
      <div className={ classNames(["formation-item", className]) } onClick={this.click}>
        <div className="formation-thumb">
          <div className={`item-${formation.id} ${size}`} />
          {/*<img src={require(`./assets/${formation.id}.png`)} />*/}
          { selected ? <i className="selected" /> : null }
        </div>
        <p>{formation.text || formation.name}</p>
      </div>
    );
  }
}

export default FormationItem;



