import { HISTORY, SIGN_IN } from '../constants/actionType';
import { createReducer } from '../utils/createReducer';

const initialState = {
  checkcount: 0,
  checklist: {},
  tarot_text: '',
  today_ischeck: '',
  today_tarot: 0
};

function checkListFormatter(checklist) {
  const result = {};
  checklist = checklist.split(';');
  checklist.pop();
  checklist.map((check) => {
    check = check.split(',');
    result[check[0].replace(/\//ig, '-')] = check[1];
  });

  return result;
}

export default createReducer(initialState, {
  [HISTORY.SUCCESS]: (state, payload) => {
    return {
      ...state,
      tarot_text: '',
      today_tarot: '',
      today_ischeck: false,
      ...payload,
      checklist: checkListFormatter(payload.checklist),
    };
  },
  [SIGN_IN.SUCCESS]: (state, payload) => {
    return {
      ...state,
      ...payload,
      checklist: checkListFormatter(payload.checklist),
      today_ischeck: true
    };
  }
});



