import * as React from 'react';
import * as classNames from 'classnames';
import * as _ from 'lodash';
import { connect } from 'react-redux';
import Slider from 'react-slick';

import Button from '../../components/Button';
import Dice from './dice';
import Tarot from '../../components/Tarot';
import Detail from './detail-frame';
import Particle from './particle';

import { SHARE_CONTENT } from '../../constants/constant';

import {
  storyResultById,
  getDailyDivine,
  cleanResult
} from '../../actions/story';
import { setShareContent } from '../../actions/global';

import './result.scss';

interface ResultProps {
  router: any;
  result: any;
  formations: Array<any>;
  params: any;
  dispatch;
  profile;
  shareContent;
};

interface ResultState {
  selectedDots: number;
  cardFlipped: boolean;
};

class Result extends React.Component<ResultProps, ResultState> {
  sliderConfig = {
    variableWidth: true,
    infinite: false,
    centerMode: true,
    dots: true,
    arrows: false
  };

  constructor(props: ResultProps) {
    super(props);

    this.state = {
      selectedDots: 0,
      cardFlipped: false
    };
  }

  componentWillUnmount() {
    const { dispatch } = this.props;

    dispatch(cleanResult());
  }

  /**
   * @todo
   * 进入页面时，首先重置数据，
   * 将一张牌以背面的形式显示在正中,
   * 接口获取到数据之后播放翻牌动画
   */
  componentDidMount() {
    const { result, router, params, dispatch, profile } = this.props;

    const { id, cardId } = params;

    const { session } = profile

    const flip = () => this.setState({ cardFlipped: true });

    if(id && session) {
      dispatch(storyResultById(session, id, flip));
    } else if(cardId && session) {
      dispatch(getDailyDivine(session, cardId, flip));
    }
  }

  componentWillReceiveProps(nextProps) {
    const { result, dispatch, params } = this.props;
    const { cardId } = params;

    if(!_.isEqual(result, nextProps.result)) {
      const { cardsInfos } = nextProps.result;
      const shareInfo = cardsInfos[0] || {};
      const shareTitle = (cardId && '神了！我今天的运势果然是...') ||
        (shareInfo && shareInfo.content && '好准！我刚刚用魔法屋测了...') ||
        '魔法屋-口袋里的占卜神器'
      ;
      const shareText = shareInfo &&
        shareInfo.content &&
        (shareInfo.content[1].content || shareInfo.content[0].content) ||
        '塔罗牌实在太准了！快来寻找属于你的答案！'
      ;
      dispatch(setShareContent({
        title: shareTitle,
        text: shareText,
        icon: SHARE_CONTENT.icon,
        link: SHARE_CONTENT.url,
        comment: `今日运势`,
        channel: 'wechat',
        type: 'webpage'
      }));
    }
  }

  /**
   * @arg {number} index 牌的序号
   */
  onSlide = (index) => {
    /**
     * @todo
     * 当选择的牌改变时，骰子和牌下方的文字替换为相应的内容
     */
    this.setState({
      selectedDots: index
    });
  }

  private onBack = () => {
    const { router } = this.props;

    router.push('/today');
  }

  render() {
    const { result, formations, router } = this.props;
    const { sliderConfig } = this;
    const { selectedDots, cardFlipped } = this.state;
    const { cardsInfos } = result;

    const formattedCards = cardsInfos || [];
    const currentCard = formattedCards[selectedDots] || {};

    let formation = result.formation && result.formation.name || result.formation;

    const selectedFormation = formation && formations.filter((f) => f.name === formation)[0];

    const { question } = router.location.query;

    const haveDiffFormations = cardsInfos && cardsInfos.length > 1
      && selectedFormation && selectedFormation.cardAmount > 1
    ;

    return (
      <div className="result">
        <div className="question-container">
          { question || result.question }
        </div>

        <div className="dice-container">
          <Dice
            type={ selectedFormation && selectedFormation.id || 0 }
            desc={ selectedFormation && `${selectedFormation.text}占卜法` || '' }
            index={ haveDiffFormations ? selectedDots : null }
            />
        </div>

        <div className="card-container">
          <Slider { ...sliderConfig } afterChange={ this.onSlide } dotsClass={ `dots selected-${selectedDots}` }>
            {
              formattedCards.map((card, index) => (
                <div key={ index } className="card-wrapper">
                  <Tarot size="large" frontImgSrc={ card.index || 0 } flipped={ cardFlipped } />
                </div>
              ))
            }
          </Slider>

          {
            formattedCards.length === 0 &&
              <div className="card-wrapper empty" />
          }

          <div className="card-name">
            { currentCard.name }
          </div>

          <div className="card-meaning">
            {
              currentCard.direction &&
                <span>
                  { `方位: ${currentCard.direction}` }
                </span>
            }
            {
              selectedFormation && selectedFormation.meanings &&
                <span>
                  { `牌义: ${selectedFormation.meanings[selectedDots]} ` }
                </span>
            }
          </div>
        </div>

        {
          _.isBoolean(currentCard.yesOrNo) &&
            <div className={ classNames(['yes-or-no-container', { yes: currentCard.yesOrNo, no: !currentCard.yesOrNo }]) } />
        }

        <div className="detail-container">
          <Detail type={ result.divine_type } content={ currentCard.content } />
        </div>

        <div className="btn-container">
          <Button
            size="medium"
            theme="brown"
            text="返回首页"
            onClick={ this.onBack }
            />
        </div>

        <div className="bg-container" />

        <Particle className="particle-container" />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    result: state.story.result,
    formations: state.story.formations,
    profile: state.profile,
    shareContent: state.global.shareContent
  };
};

export default connect(mapStateToProps)(Result);



