import * as React from 'react';
import delay from '../../../utils/delay';

import './calendarItem.scss';

interface CalendarItemProps {
  tarot?: string;
  date?: string;
  highlight?: boolean;
  dark?: boolean;
  onClick?: Function;
}

interface CalendarItemState {
  showTip: boolean;
}

class CalendarItem extends React.Component<CalendarItemProps, CalendarItemState> {
  constructor(props: CalendarItemProps) {
    super(props);
    this.state = {
      showTip: false
    };
    this.clickHandler = this.clickHandler.bind(this);
  }

  async showTarotTip() {
    this.setState({showTip: true});
    await delay(2000);
    this.setState({showTip: false});
  }

  clickHandler() {
    if (this.props.onClick && this.props.tarot) {
      this.props.onClick(this.props.tarot, this.props.date);
    } else {
      this.showTarotTip();
    }
  }

  render() {
    const { date, highlight, dark, onClick } = this.props;
    const { showTip } = this.state;
    const tarot = this.props.tarot as any;
    const img = tarot === '' ? '' : tarot % 22;
    return (
      <div className={`calendar-item ${highlight ? 'highlight' : ''} ${tarot > 22 ? 'rotate' : ''} ${dark ? 'dark' : ''}`} onClick={this.clickHandler}>
        {
          tarot ?
          <div  className={ `calendar-item-tarot tarot-${img}` }>
            {/*<img src={require(`../../../assets/tarot/tarot-${img}.png`)} />*/}
            <div className="tarot-img" />
          </div> : null
        }
        {
          date ? <p className="calendar-item-date">{date}日</p> : null
        }
        {
          dark ? <div className="dark-mask" /> : null
        }
        { showTip ? <div className="calendar-item-tip">当天没有测算哦</div> : null }
      </div>
    );
  }
}

export default CalendarItem;



