import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as classNames from 'classnames';
import { connect } from 'react-redux';
import CommunityItem from '../../components/CommunityItem';
import * as CommunityActions from '../../actions/community';
import Nav, {NavProps} from '../../components/Nav';

import './community.scss';

interface CommunityProps {
  dispatch;
  profile;
  community;
  router;
  params;
}

interface CommunityState {
  isLoading: boolean;
  isSelf: boolean;
}

class Community extends React.Component<CommunityProps, CommunityState> {
  constructor(props: CommunityProps) {
    super(props);
    this.state = {
      isLoading: false,
      isSelf: props.params.self
    };
    this.getCommunities = this.getCommunities.bind(this);
    this.transitionToResult = this.transitionToResult.bind(this);
    this.transitionTo = this.transitionTo.bind(this);
  }

  componentDidMount() {
    this.getCommunities();

    const { list } = this.refs as any;

    const listNode = ReactDOM.findDOMNode(list);

    listNode.addEventListener('scroll', (e) => {
      this.scrollHandler(e);
    });
  }

  transitionTo() {
    const { router } = this.props;
    router.push('/question/input');
  }

  scrollHandler(event) {
    if (this.state.isLoading) {
      return;
    }

    if (event.target.scrollTop + event.target.clientHeight + 50 >= event.target.scrollHeight) {
      this.getCommunities();
    }
  }

  getCommunities() {
    if (this.state.isLoading || this.props.community.isEnd) {
      return;
    }
    this.setState({
      isLoading: true
    }, () => {
      const pagination = {...this.props.community.pagination.list, self: this.state.isSelf ? 'yes' : 'no'};
      pagination.page = pagination.page + 1;
      this.props.dispatch(CommunityActions.getCommunityList(this.props.profile.session, pagination, () => {
        this.setState({
          isLoading: false
        });
      }));
    });
  }

  transitionToResult(item) {
    const { router } = this.props;
    router.push(`/result/community/${item.question_id}?isOthers=true`);
  }

  componentWillUnmount() {
    this.props.dispatch(CommunityActions.clearRecommendList());
  }

  render() {
    const { community } = this.props;
    const { isSelf } = this.state;
    return (
      <div className={ classNames('community', isSelf ? 'community-self' : '') }>
        <div ref="list" className="community-list">
          {
            !isSelf &&  <header />
          }
          {
            community.list.map((communityItem, index) => {
              return <CommunityItem key={index} {...communityItem} onClick={this.transitionToResult.bind(this, communityItem)} />;
            })
          }
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    profile: state.profile,
    community: state.community
  };
};

export default connect(mapStateToProps)(Community);



