import { takeEvery, put, call, fork } from 'redux-saga/effects';

import { GET_COMMUNITY_LIST, GET_COMMUNITY_RECOMMAND_LIST, CLEAR_COMMUNITY_LIST } from '../constants/actionType';
import CommunityService from '../services/community';

export function* getCommunityList(action) {
  try {
    const [ list ] = yield [
      call(CommunityService.getList, action.payload.session, action.payload.pagination)
    ];

    yield put({
      type: GET_COMMUNITY_LIST.SUCCESS,
      payload: {
        list,
        pagination: action.payload.pagination
      }
    });
    if (action.payload.callback) {
      action.payload.callback();
    }
  } catch (err) {
    yield put({ type: GET_COMMUNITY_LIST.FAILED });
  }
}

export function* getCommunityRecommandList(action) {
  try {
    const [ list ] = yield [
      call(CommunityService.getRecommendList, action.payload.session, action.payload.pagination)
    ];

    yield put({
      type: GET_COMMUNITY_RECOMMAND_LIST.SUCCESS,
      payload: {
        recommand: list
      }
    });
    if (action.payload.callback) {
      action.payload.callback();
    }
  } catch (err) {
    yield put({ type: GET_COMMUNITY_RECOMMAND_LIST.FAILED });
  }
}

export function* clearCommunityList() {
  yield put({ type: CLEAR_COMMUNITY_LIST.SUCCESS });
}

export function* watchGetCommunityList() {
  yield takeEvery(GET_COMMUNITY_LIST.ACTION, getCommunityList);
}

export function* watchClearCommunityList() {
  yield takeEvery(CLEAR_COMMUNITY_LIST.ACTION, clearCommunityList);
}

export function* watchGetCommunityRecommandList() {
  yield takeEvery(GET_COMMUNITY_RECOMMAND_LIST.ACTION, getCommunityRecommandList);
}

export default function* users() {
  yield fork(watchGetCommunityList);
  yield fork(watchGetCommunityRecommandList);
  yield fork(watchClearCommunityList);
}



