import * as React from 'react';
import delay from '../../../utils/delay';

import Button from '../../../components/Button';

import './todayPlay.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示
export enum todayPlayStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE,
  VISIBLE_HIDDEN
}

interface TodayPlayProps {
  status: todayPlayStatus;
  onClick;
}

interface TodayPlayState {
  status: todayPlayStatus;
}

export class TodayPlay extends React.Component<TodayPlayProps, TodayPlayState> {
  constructor(props: TodayPlayProps) {
    super(props);
    this.state = {
      status: todayPlayStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  async componentDidMount() {
    this.changeStatus([0, todayPlayStatus.MOUNTED]);
  }

  getClassByStatus(status) {
    switch(status) {
    case todayPlayStatus.HIDDEN:
    case todayPlayStatus.MOUNTED:
      return '';
    case todayPlayStatus.VISIBLE:
      return 'visible';
    case todayPlayStatus.VISIBLE_HIDDEN:
      return 'visible-hidden';
    default:
      return '';
    }
  }

  render() {
    const { onClick } = this.props;
    const { status } = this.state;
    if (status === todayPlayStatus.HIDDEN) {
      return null;
    }
    return (
      <div className={`today-play ${this.getClassByStatus(status)}`}>
        <Button text="今日塔罗" size="small" theme="green" onClick={() => { onClick(); }} />
      </div>
    );
  }
}



