import {
  QUESTION_GET_RESULT,
  NEWBIE_QUESTION_GET_RESULT,
} from '../constants/actionType';
import { createReducer } from '../utils/createReducer';

const initialState = {
  questions: [],
};

export default createReducer(initialState, {
  [QUESTION_GET_RESULT.SUCCESS]: (state, { result }) => {
    return {
      ...state,
      questions: result.question && result.question.split('#')
    };
  },
  [NEWBIE_QUESTION_GET_RESULT.SUCCESS]: (state, { result }) => {
    return {
      ...state,
      questions: result.question && result.question.split('#')
    };
  },
});



