import { put } from 'redux-saga/effects'

import * as Fetch from '../utils/fetch'
import { actionType, saga, reducer } from '../utils/redux-decorators'
import { prefix } from '../constants/api'

const SET_BIRTHDAY_API = prefix('app_server.php?command=')

export default class HoroscopeSelectorService {
  @actionType('SET_BIRTHDAY')
  static ACTION

  // 可以传actionType，不传的话，方法名就是actionType
  @saga()
  static * SET_BIRTHDAY(action) {
    try {
      Fetch.postForm(SET_BIRTHDAY_API, {
        command: '1017',
        ...(({ birthday, session }) => ({ birthday, session }))(action.payload)
      })

      yield put({
        type: HoroscopeSelectorService.ACTION.SUCCESS
      })

      if (action.payload.callback) {
        action.payload.callback()
      }
    } catch (err) {
      yield put({type: HoroscopeSelectorService.ACTION.FAILED})
    }
  }

  // 可以传actionType，不传的话，方法名就是actionType
  @reducer()
  SET_BIRTHDAY_SUCCESS(state, payload) {
    return {
      ...state,
      ...payload
    }
  }
}




