import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as classNames from 'classnames';
import * as _ from 'lodash';

export const CARD_MODE = {
  APPEAR: 'appear',
  DISAPPEAR: 'disappear',
  SELECTED: 'selected',
  TOHEAP: _.range(1,8).map((v) => `to-heap-${v}`)
};

interface CardProps {
  style?: Object,
  className?: string,
  mode?: Array<string>,
  onClick?: Function
};

interface CardState {
  stickStyle: Object
};

class Card extends React.Component<CardProps, CardState> {
  static defaultProps = {
    mode: '',
    style: {},
    className: '',
    onClick: () => {}
  };

  private cardRef: HTMLDivElement;

  constructor(props: CardProps) {
    super(props);

    this.state = {
      stickStyle: {}  
    };
  }

  private onClick = (cb) => {
    cb && cb();
  }

  public sticky = (offset = 0) => {
    const { cardRef } = this;

   const { top, left } = ReactDOM.findDOMNode(cardRef).getBoundingClientRect();

    this.setState({
      stickStyle: {
        position: 'fixed',
        top: top,
        left: left,
        zIndex: 100
      }
    });
  }

  render() {
    const { mode, style, className, onClick } = this.props;

    const { stickStyle } = this.state;

    return (
      <div 
        ref={ (elem) => this.cardRef = elem }
        style={{
          ...style,
          ...stickStyle
        }}
        className={ classNames("card", mode, className) }
        onClick={ this.onClick.bind(this, onClick) }
        />
    );
  }
}

export default Card;


