import { GET_COMMUNITY_LIST, GET_COMMUNITY_RECOMMAND_LIST, CLEAR_COMMUNITY_LIST } from '../constants/actionType';

export function getCommunityList(session, pagination, callback) {
  return {
    type: GET_COMMUNITY_LIST.ACTION,
    payload: {
      pagination,
      session,
      callback
    }
  };
}

export function getRecommendList(session, pagination, callback) {
  return {
    type: GET_COMMUNITY_RECOMMAND_LIST.ACTION,
    payload: {
      pagination,
      session,
      callback
    }
  };
}

export function clearRecommendList() {
  return {
    type: CLEAR_COMMUNITY_LIST.ACTION
  };
}



