import * as React from 'react';
import * as moment from 'moment';
import Container from '../';
import Nav, { NavProps } from '../../../components/Nav';
import DateSelector from '../../../components/DateSelector/DateSelector';
import Button from '../../../components/Button';
import withUpdate from '../withUpdate';

import './accountBirthday.scss';

interface AccountBirthdayProps {
  profile;
  update;
}

interface AccountBirthdayState {
  birthday: string;
}

export default withUpdate(class AccountBirthday extends React.Component<AccountBirthdayProps, AccountBirthdayState> {
  constructor(props: AccountBirthdayProps) {
    super(props);
    this.state = {
      birthday: props.profile.birthday
    };

    this.submit = this.submit.bind(this);
    this.handleSelectDate = this.handleSelectDate.bind(this);
  }

  handleSelectDate(year, month, date) {
    this.setState({
      birthday: [year, month, date].join('-')
    });
  }

  submit() {
    this.props.update({
      birthday: this.state.birthday
    });
  }

  render() {
    const { birthday } = this.state;

    return (
      <Container className="account-birthday">
        <DateSelector
          onSelectDate={this.handleSelectDate}
          initDate={ birthday }
          endDate={ moment().format('YYYY-MM-DD') }
          startDate="1950-01-01"
        />
        <Button theme="green-ml" size="medium-large" text="确认" onClick={this.submit} />
      </Container>
    );
  }
});



