import * as React from 'react'
import Gesture from 'rc-gesture'

import './column.scss'

enum ScrollSpeed {
  HighSpeed = 'rcc-column-row-hs',
  MediumSpeed = 'rcc-column-row-ms',
  LowSpeed = 'rcc-column-row-ls',
  HighSpeedTime = 100,
  MediumSpeedTime = 150,
  LowSpeedTime = 300
}

enum ScrollDirection {
  Up = 'Up',
  Down = 'Down'
}

export type ColumnProps = {
  values: string[]
  initValue: string
  recyclable?: boolean,
  onSelectValue?: Function
}

type ColumnState = {
  valueCount: number,
  values: string[]
}

class Column extends React.Component<ColumnProps, ColumnState> {
  constructor(props) {
    super(props)

    let values = this.getInnerValues(props.values)

    this.state = {
      valueCount: values.length,
      values: values
    }

    this.innerState.currentIndex = values.findIndex((value) => {
      return this.props.initValue === value
    })

    this.scrollUp = this.scrollUp.bind(this)
    this.scrollDown = this.scrollDown.bind(this)
  }

  // 没有的话使用ref dom方法会报错
  refs = {}

  innerState = {
    preRow: null,
    firstRow: null,
    secondRow: null,
    currentRow: null,
    fourthRow: null,
    fifthRow: null,
    nextRow: null,
    velocity: 0,
    currentIndex: 0,
    scrolling: false,
    scrollSpeed: ScrollSpeed.LowSpeed,
    stopSequenceScroll: null
  }

  setValues(values) {
    let vs = this.getInnerValues(values)

    if (this.innerState.currentIndex > vs.length - 1) {
      this.innerState.currentIndex = vs.length - 1
    }

    this.setState({
      valueCount: vs.length,
      values: vs
    }, this.setPosition)
  }

  getInnerValues(values) {
    let vs = values
    if (!this.props.recyclable) {
      vs = ['',''].concat(values).concat(['',''])
    }

    return vs
  }

  componentDidMount() {
    this.setPosition()
  }

  setPosition() {
    this.doOneScroll(ScrollSpeed.LowSpeed, null, null);
  }

  doOneScroll(scrollSpeed, direction, cb) {
    if (direction === ScrollDirection.Up) {
      // 最末尾状态，不能循环就返回
      if (this.innerState.currentIndex >= this.state.valueCount - 3) {
        if (!this.props.recyclable) {
          // this.refs['eoc'].classList.add("eoc-active")
          cb && cb('EOC')
          return
        }
      }

      this.innerState.currentIndex = this.innerState.currentIndex === this.state.valueCount - 1 ? 0 : this.innerState.currentIndex + 1
    } else if (direction === ScrollDirection.Down) {
      // 最前面状态，不能循环就返回
      if (this.innerState.currentIndex <= 2) {
        if (!this.props.recyclable) {
          // this.refs['hoc'].classList.add("hoc-active")
          cb && cb('HOC')
          return
        }
      }

      this.innerState.currentIndex = this.innerState.currentIndex === 0 ? this.state.valueCount - 1 : this.innerState.currentIndex - 1
    }

    this.resetScorllStatus()

    this.innerState.scrollSpeed = scrollSpeed

    let preRowIndex = this.innerState.currentIndex - 3
    if (preRowIndex < 0) {
      preRowIndex = this.state.valueCount + this.innerState.currentIndex - 3
    }
    let preRow = this.refs['row' + preRowIndex]
    preRow.classList.add(scrollSpeed)
    preRow.classList.add("rcc-column-row-show")
    preRow.classList.add("rcc-column-row-pre")
    this.innerState.preRow = preRow

    let firstRowIndex = this.innerState.currentIndex - 2
    if (firstRowIndex < 0) {
      firstRowIndex = this.state.valueCount + this.innerState.currentIndex - 2
    }
    let firstRow = this.refs['row' + firstRowIndex]
    firstRow.classList.add(scrollSpeed)
    firstRow.classList.add("rcc-column-row-show")
    firstRow.classList.add("rcc-column-row-1st")
    this.innerState.firstRow = firstRow

    let secondRowIndex = this.innerState.currentIndex - 1
    if (secondRowIndex < 0) {
      secondRowIndex = this.state.valueCount + this.innerState.currentIndex - 1
    }
    let secondRow = this.refs['row' + secondRowIndex]
    secondRow.classList.add(scrollSpeed)
    secondRow.classList.add("rcc-column-row-show")
    secondRow.classList.add("rcc-column-row-2nd")
    this.innerState.secondRow = secondRow

    let currentRow = this.refs['row' + this.innerState.currentIndex]
    currentRow.classList.add(scrollSpeed)
    currentRow.classList.add("rcc-column-row-show")
    currentRow.classList.add("rcc-column-row-3rd")
    this.innerState.currentRow = currentRow

    let fourthRowIndex = this.innerState.currentIndex + 1
    if (fourthRowIndex > this.state.valueCount - 1) {
      fourthRowIndex = this.innerState.currentIndex - this.state.valueCount + 1
    }
    let fourthRow = this.refs['row' + fourthRowIndex]
    fourthRow.classList.add(scrollSpeed)
    fourthRow.classList.add("rcc-column-row-show")
    fourthRow.classList.add("rcc-column-row-4th")
    this.innerState.fourthRow = fourthRow

    let fifthRowIndex = this.innerState.currentIndex + 2
    if (fifthRowIndex > this.state.valueCount - 1) {
      fifthRowIndex = this.innerState.currentIndex - this.state.valueCount + 2
    }
    let fifthRow = this.refs['row' + fifthRowIndex]
    fifthRow.classList.add(scrollSpeed)
    fifthRow.classList.add("rcc-column-row-show")
    fifthRow.classList.add("rcc-column-row-5th")
    this.innerState.fifthRow = fifthRow

    let nextRowIndex = this.innerState.currentIndex + 3
    if (nextRowIndex > this.state.valueCount - 1) {
      nextRowIndex = this.innerState.currentIndex - this.state.valueCount + 3
    }
    let nextRow = this.refs['row' + nextRowIndex]
    nextRow.classList.add(scrollSpeed)
    nextRow.classList.add("rcc-column-row-show")
    nextRow.classList.add("rcc-column-row-next")
    this.innerState.nextRow = nextRow

    this.props.onSelectValue && this.props.onSelectValue(this.state.values[this.innerState.currentIndex])

    let completeTime = 0
    if (scrollSpeed === ScrollSpeed.HighSpeed) {
      completeTime = ScrollSpeed.HighSpeedTime
    } else if (scrollSpeed === ScrollSpeed.MediumSpeed) {
      completeTime = ScrollSpeed.MediumSpeedTime
    } else if (scrollSpeed === ScrollSpeed.LowSpeed) {
      completeTime = ScrollSpeed.LowSpeedTime
    }

    setTimeout(() => {
      cb && cb()
    }, completeTime)
  }

  stopOneScroll() {
    let scrollSpeed = this.innerState.scrollSpeed
    this.innerState.preRow.classList.remove(scrollSpeed)
    this.innerState.firstRow.classList.remove(scrollSpeed)
    this.innerState.secondRow.classList.remove(scrollSpeed)
    this.innerState.currentRow.classList.remove(scrollSpeed)
    this.innerState.fourthRow.classList.remove(scrollSpeed)
    this.innerState.fifthRow.classList.remove(scrollSpeed)
    this.innerState.nextRow.classList.remove(scrollSpeed)
    this.refs['hoc'].classList.remove("hoc-active")
    this.refs['eoc'].classList.remove("eoc-active")
  }

  resetScorllStatus() {
    let scrollSpeed = this.innerState.scrollSpeed

    if (this.innerState.preRow) {
      this.innerState.preRow.classList.remove('rcc-column-row-pre')
      this.innerState.preRow.classList.remove('rcc-column-row-show')
      this.innerState.preRow.classList.remove(scrollSpeed)
    }
    if (this.innerState.firstRow) {
      this.innerState.firstRow.classList.remove('rcc-column-row-1st')
      this.innerState.firstRow.classList.remove('rcc-column-row-show')
      this.innerState.firstRow.classList.remove(scrollSpeed)
    }
    if (this.innerState.secondRow) {
      this.innerState.secondRow.classList.remove('rcc-column-row-2nd')
      this.innerState.secondRow.classList.remove('rcc-column-row-show')
      this.innerState.secondRow.classList.remove(scrollSpeed)
    }
    if (this.innerState.currentRow) {
      this.innerState.currentRow.classList.remove('rcc-column-row-3rd')
      this.innerState.currentRow.classList.remove('rcc-column-row-show')
      this.innerState.currentRow.classList.remove(scrollSpeed)
    }
    if (this.innerState.fourthRow) {
      this.innerState.fourthRow.classList.remove('rcc-column-row-4th')
      this.innerState.fourthRow.classList.remove('rcc-column-row-show')
      this.innerState.fourthRow.classList.remove(scrollSpeed)
    }
    if (this.innerState.fifthRow) {
      this.innerState.fifthRow.classList.remove('rcc-column-row-5th')
      this.innerState.fifthRow.classList.remove('rcc-column-row-show')
      this.innerState.fifthRow.classList.remove(scrollSpeed)
    }
    if (this.innerState.nextRow) {
      this.innerState.nextRow.classList.remove('rcc-column-row-next')
      this.innerState.nextRow.classList.remove('rcc-column-row-show')
      this.innerState.nextRow.classList.remove(scrollSpeed)
    }
  }

  scrollUp(gestureState) {
    if (this.innerState.stopSequenceScroll) {
      this.innerState.stopSequenceScroll()
      this.innerState.stopSequenceScroll = null
    }

    this.innerState.velocity = gestureState.moveStatus.velocity
    this.innerState.stopSequenceScroll = this.doSequenceScroll(this.innerState.velocity, ScrollDirection.Up)
  }

  scrollDown(gestureState) {
    if (this.innerState.stopSequenceScroll) {
      this.innerState.stopSequenceScroll()
      this.innerState.stopSequenceScroll = null
    }

    this.innerState.velocity = gestureState.moveStatus.velocity
    this.innerState.stopSequenceScroll = this.doSequenceScroll(this.innerState.velocity, ScrollDirection.Down)
  }

  doSequenceScroll(velocity, direction: ScrollDirection) {
    let stopFlag = false
    let inertiaSequence = this.getInertiaSequence(velocity);

    this.innerState.scrolling = true

    let currentScrollIndex = 0
    let currentScrollLength = inertiaSequence.length
    let cb = (Err) => {
      if (stopFlag) {
        this.stopOneScroll()
        clearScrollState()
        return
      }

      if (Err) {
        clearScrollState()
        return
      }

      currentScrollIndex++
      if (currentScrollIndex > currentScrollLength - 1) {
        clearScrollState()
        return
      }

      this.doOneScroll(inertiaSequence[currentScrollIndex], direction, cb)
    }

    let clearScrollState = () => {
      currentScrollIndex = 0
      inertiaSequence = null
      currentScrollLength = 0
      this.innerState.scrolling = false
    }

    let stopSequenceScroll = () => {
      stopFlag = true
    }

    this.doOneScroll(inertiaSequence[currentScrollIndex], direction, cb)

    return stopSequenceScroll
  }

  getInertiaSequence(velocity: number) {
    // 滚行滚动规则
    // 速度<0.6        滚动1格
    // 0.6~1  滚动1秒  +中速1格
    // 1~2.5  滚动2秒  +中速3格
    // 2.5~4  滚动3秒  +1秒高速
    // 4~6    滚动4秒  +1秒高速
    // > 6    滚动5秒  +1秒高速
    let scrolls = null

    if (velocity > 0) {
      scrolls = [ScrollSpeed.LowSpeed]
    }
    if (velocity >= 0.6) {
      scrolls = new Array(1).fill(ScrollSpeed.MediumSpeed).concat(scrolls)
    }
    if (velocity >= 1) {
      scrolls = new Array(3).fill(ScrollSpeed.MediumSpeed).concat(scrolls)
    }
    if (velocity >= 2.5) {
      scrolls = new Array(10).fill(ScrollSpeed.HighSpeed).concat(scrolls)
    }
    if (velocity >= 4) {
      scrolls = new Array(10).fill(ScrollSpeed.HighSpeed).concat(scrolls)
    }
    if (velocity >= 6) {
      scrolls = new Array(10).fill(ScrollSpeed.HighSpeed).concat(scrolls)
    }

    return scrolls
  }

  render() {
    return <Gesture onSwipeUp={this.scrollUp} onSwipeDown={this.scrollDown}>
      <div className="rcc-column">
        <div ref="hoc" className="rcc-column-row rcc-column-row-hoc"></div>
        {
          this.state.values.map((value, index) => {
            return <div className="rcc-column-row" key={index}
                        ref={'row' + index}>{value}</div>
          })
        }
        <div ref="eoc" className="rcc-column-row rcc-column-row-eoc"></div>
      </div>
    </Gesture>
  }
}

const mapStateToProps = (state, ownProps: ColumnProps) => {
  return {}
}

// export default connect(mapStateToProps)(Column)
export default Column



