import * as React from 'react';
import delay from '../../../utils/delay';

import './todayHeader.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示
export enum todayHeaderStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE,
  STATIC
}

interface TodayHeaderProps {
  profile;
  status: todayHeaderStatus;
}

interface TodayHeaderState {
  status: todayHeaderStatus;
}

export class TodayHeader extends React.Component<TodayHeaderProps, TodayHeaderState> {
  constructor(props: TodayHeaderProps) {
    super(props);
    this.state = {
      status: todayHeaderStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  componentDidMount() {
  }

  getClassByStatus(status) {
    switch (status) {
    case todayHeaderStatus.HIDDEN:
    case todayHeaderStatus.MOUNTED:
      return '';
    case todayHeaderStatus.VISIBLE:
      return 'visible';
    case todayHeaderStatus.STATIC:
      return 'static';
    default:
      return '';
    }
  }

  render() {
    const { profile } = this.props;
    const { status } = this.state;
    if (status === todayHeaderStatus.HIDDEN) {
      return null;
    }
    return (
        <h1 className={`today-header ${this.getClassByStatus(status)}`}>
          <p>欢迎您，<b>{profile.nickname}</b></p>
        </h1>
    );
  }
}



