import {
  QUESTION_GET_RESULT,
  NEWBIE_QUESTION_GET_RESULT,
} from '../constants/actionType';

export function questionResult({ formation, divineType: divine_type }, callback) {
  return {
    type: QUESTION_GET_RESULT.ACTION,
    payload: {
      formation,
      divine_type,
      callback,
    }
  }
}

export function newbieQuestionResult({ cardsCount: cards_count, divineType: divine_type }, callback) {
  return {
    type: NEWBIE_QUESTION_GET_RESULT.ACTION,
    payload: {
      cards_count,
      divine_type,
      callback,
    }
  }
}


