global.Promise = require('es6-promise').Promise;
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { hashHistory, browserHistory } from 'react-router';
import { AppContainer } from 'react-hot-loader';
import { syncHistoryWithStore } from 'react-router-redux';
import { useBasename } from 'history';

import { preserveQueryHistory } from '../utils/history-enhance';
import Root from '../containers/Root';
import store from '../store/configureStore';

import 'slick-carousel/slick/slick.scss';
import 'slick-carousel/slick/slick-theme.scss';

// import image for share page
import '../assets/share/share01.jpg';
import '../assets/share/share02.jpg';
import '../assets/share/share03.jpg';
import '../assets/share/share04.jpg';
import '../assets/logo-to-share.jpg';

const history = process.env.NODE_ENV === 'production' ?
  preserveQueryHistory(browserHistory, ['session', 'isnewplayer']) :
  hashHistory
;

const appHistory: boolean = syncHistoryWithStore(history, store);

function renderApp(RootComponent: any) {
  const target = document.getElementById('root');

  if (target) {
    ReactDOM.render(
      <AppContainer>
        <RootComponent store={store} history={appHistory} />
      </AppContainer>,
      target
    );
  }
}

renderApp(Root);

if (module.hot) {
  module.hot.accept(
    '../containers/Root',
    () => renderApp(require('../containers/Root'))
  );
}



