import * as Fetch from '../utils/fetch';
import { SIGN_IN, HISTORY } from '../constants/api';

const TarotService = {
  signIn(session) {
    return Fetch.postForm(SIGN_IN, {session, command: 1021});
  },
  history(session) {
    return Fetch.postForm(HISTORY, {session, command: 1023});
  }
};

export default TarotService;



