import * as React from 'react';
import * as classNames from 'classnames';
import * as _ from 'lodash';
import {Link} from 'react-router';

import './nav.scss';

const Base64 = require('js-base64').Base64;

export type NavdisplayConfig = {
  type: string
  content?: string
  to?: string
}

export interface NavProps {
  left?: NavdisplayConfig
  title?: string
  right?: NavdisplayConfig
  visible?: boolean
  fixed?: boolean
  shareContent?: string
  router: any
}

export const SHARE_URL = (data) => {
  const encoded = [];  
  for(let key in data) {
    if(data.hasOwnProperty(key)) {
      encoded.push({
        key,
        value: Base64.encode(data[key])
      });
    }
  }

  return encoded.reduce((prev, curr, index) => {
    if(index === 0) {
      prev += '?';
    } else {
      prev += '&';
    }
    return prev + `${curr.key}=${curr.value.replace(/=/g, '$')}`;
  }, 'UniWebView://share');
};

class Nav extends React.Component<NavProps, undefined> {
  renderLeft(leftConfig: NavdisplayConfig, cb): JSX.Element {
    return this[`${leftConfig.type}TypeLeft`](leftConfig, cb)
  }

  backTypeLeft(leftConfig, backwardCb): JSX.Element {
    return (leftConfig && leftConfig.to) 
      && (<Link to={leftConfig.to} className="left back"></Link>)
      || (<a onClick={ backwardCb } className="left back" />)
    ;
  }

  homeTypeLeft(leftConfig): JSX.Element {
    return (<Link to="today" className="left home"></Link>);
  }

  textTypeLeft(leftConfig: NavdisplayConfig): JSX.Element {
    return (<Link to={leftConfig.to} className="left">{leftConfig.content}</Link>)
  }

  renderRight(rightConfig: NavdisplayConfig, cb): JSX.Element {
    return this[`${rightConfig.type}TypeRight`](rightConfig, cb);
  }

  shareTypeRight(rightConfig: NavdisplayConfig, cb): JSX.Element {
    return rightConfig && rightConfig.to
      && (<Link to={rightConfig.to} className="right share" />)
      || (<a onClick={ cb } className="right share" />)
    ;
  }

  textTypeRight(rightConfig: NavdisplayConfig): JSX.Element {
    return (<Link to={rightConfig.to} className="right">{rightConfig.content}</Link>)
  }

  render() {
    const { router, visible, fixed, shareContent } = this.props;

    return (
      <nav className={ classNames("nav", { invisible: !visible }, { fixed: fixed }) }>
        {this.props.left && this.renderLeft(this.props.left, router.goBack )}
        <div className="title">{this.props.title}</div>
        {this.props.right && this.renderRight(this.props.right, () => {
          window.location.href = SHARE_URL(shareContent as any);
        })}
      </nav>
    );
  }
}

export default Nav;



