import delay from '../../utils/delay';
import * as _ from 'lodash';

/**
 * @arg {array} statusList Array<status>
 * @arg {object} status { duration, stateName, status }
 */
export default async (statusList, setState) => {
  const zip = (arr: Array<Object>): any => {
    return arr.reduce((prev, value) => {
      return {
        ...prev,
        ...value
      };
    }, {});
  };

  const changeStatus = async ({ duration, states }) => {
    states && setState(zip(states));

    if(_.isFunction(duration)) {
      await duration();
    } else {
      await delay(duration * 1);
    }
  };

  for(let i = 0; i < statusList.length; i++) {
    await changeStatus(statusList[i]);
  }
}



