const MAX_LEN = 16;
const MIN_LEN = 4;

export function calStrLength(str = '') {
  const reg = /^[^\x00-\xff]$/; // 匹配双字节字符
  return str
    .split('')
    .reduce((prev, next) => {
      return prev + (reg.test(next) ? 2 : 1);
    }, 0);
}

export function hasSpace(str = '') {
  return /\s/.test(str);
}

export function validateName(str: string, label = '') {
  const len = calStrLength(str);

  if (hasSpace(str)) {
    return `${label}不能包含空格`;
  }

  if (len < MIN_LEN) {
    return `${label}至少4个字符, 每个汉字为2个字符`;
  }

  if (len > MAX_LEN) {
    return `${label}最多16个字符, 每个汉字为2个字符`;
  }
}

export function validatePhone(str: string, label = '') {
  const reg = /^1\d{10}$/;
  if (!reg.test(str)) {
    return `请输入正确的${label}`;
  }
}



