import * as decoder from 'js-base64';
import {
  STORY_MODE_SELECT,
  STORY_CARDS_COUNT_SELECT,
  STORY_QUESTION_TYPE_SELECT,
  STORY_QUESTION_INPUT,
  STORY_FORMATION_SELECT,
  STORY_PRIVATE,
  STORY_GET_RESULT,
  NEWBIE_QUESTION_GET_RESULT,
  GET_DAILY_DIVINE,
  GET_RESULT_BY_ID,
  CLEAN_RESULT
} from '../constants/actionType';
import { createReducer } from '../utils/createReducer';

const formations = [
  { id: 0, name: '是与否', text: '是与否', domain: '单纯是与否问题，适用于各方面。', cardAmount: 1, meanings: ['提示'] },
  { id: 0, name: '单张牌', text: '单张牌', domain: '根据牌本身的意义来解释所问问题。', cardAmount: 1, meanings: ['运势'] },
  { id: 1, name: '四要素', text: '四要素', domain: '一般是分析在未来的某个时候可能出现的某种状况而经常采用的牌阵。', cardAmount: 4, meanings: ['行动', '沟通', '情感', '物质'] },
  { id: 2, name: '圣三角', text: '圣三角', domain: '根据分析事物的发展，来让自己明白下一步该如何做，答案清晰明确。', cardAmount: 3, meanings: ['过去', '现在', '未来'] },
  { id: 3, name: '处境马蹄牌阵', text: '处境马蹄', domain: '对当下情况的分析描述，而对你的行为做出指导。', cardAmount: 6, meanings: ['现在', '可预知的情况', '不可预知的情况', '发展的方向', '结果', '心态'] },
  { id: 4, name: '六芒星占卜', text: '六芒星', domain: '把事情的来龙去脉了解清楚，判断事情走向，在此基础上选择出一个好的解决方法。', cardAmount: 7, meanings: ['过去', '现在', '未来', '原因', '环境', '对策', '你的希望或恐惧'] },
  { id: 5, name: '大十字阵', text: '大十字', domain: '分析事物的各种原因以及结果来做出指导。', cardAmount: 5, meanings: ['外在原因', '外在原因', '发生的原因', '解决方式', '结果'] },
  { id: 6, name: '二择一占卜法', text: '二择一', domain: '都是在处于两难选择时，帮助我们做出一项相对好一些的选择。', cardAmount: 5, meanings: ['自身状况', '与A的现况', '与B的现况', '与A的未来', '与B的未来']},
  { id: 7, name: '塔罗钻石', text: '钻石', domain: '类似圣三角，通过现状、过去、未来给出答案，比圣三角对现状分析更详细，也多用于是非选择题。', cardAmount: 4, meanings: ['过去', '现在', '现在', '未来']},
  { id: 8, name: '塔罗牌吉普赛十字牌阵', text: '吉普赛十字', domain: '各方面全方位分析，特别针对感情人际问题的牌阵。', cardAmount: 5, meanings: ['心态', '现在', '举措', '环境', '未来'] },
  { id: 9, name: '幸运五指占卜法', text: '幸运五指', domain: '占卜运程的方法，接下来五周的运势。', cardAmount: 5, meanings: ['本周运势', '下周运势', '第二周运势', '第三周运势', '第四周运势'] },
];

const questionTypes = [
  { id: 0, name: '爱情' },
  { id: 1, name: '运势' },
  { id: 2, name: '事业' },
  { id: 3, name: '财运' },
  { id: 4, name: '人际' },
  { id: 5, name: '健康' }
];

const initialState = {
  mode: '',
  cardsCount: 0,
  formation: formations[0],
  questionType: questionTypes[0],
  question: '',
  result: {},
  formations,
  questionTypes,
  isPrivate: true
};

export default createReducer(initialState, {
  [STORY_MODE_SELECT.SUCCESS]: (state, payload) => {
    return {
      ...state,
      mode: payload.mode
    };
  },
  [STORY_CARDS_COUNT_SELECT]: (state, payload) => {
    return {
      ...state,
      cardsCount: payload.cardsCount
    };
  },
  [STORY_QUESTION_TYPE_SELECT.SUCCESS]: (state, payload) => {
    return {
      ...state,
      questionType: payload.questionType
    };
  },
  [STORY_QUESTION_INPUT.SUCCESS]: (state, payload) => {
    return {
      ...state,
      question: payload.question
    };
  },
  [STORY_FORMATION_SELECT.SUCCESS]: (state, payload) => {
    return {
      ...state,
      formation: payload.formation,
      cardsCount: payload.formation.cardAmount
    };
  },
  [STORY_PRIVATE.SUCCESS]: (state, payload) => {
    return {
      ...state,
      isPrivate: payload.isPrivate
    };
  },
  [STORY_GET_RESULT.SUCCESS]: (state, payload) => {
    const { result } = payload;

    return {
      ...state,
      result
    };
  },
  [NEWBIE_QUESTION_GET_RESULT.SUCCESS]: (state, payload) => {
    const { result } = payload;

    return {
      ...state,
      formation: { name: result.formation }
    };
  },
  [GET_DAILY_DIVINE.SUCCESS]: (state, payload) => {
    const { cardsInfos, card } = payload;

    return {
      ...state,
      formation: formations[0],
      result: {
        // cards_infos_array: [`##${card}#` + result.card_infos.split('#').slice(1).join('#')],
        cardsInfos,
        question: state.question,
        divine_type: '运势'
      }
    };
  },
  [GET_RESULT_BY_ID.SUCCESS]: (state, payload) => {
    try {
      return {
        ...state,
        result: payload
      };
    } catch(e) {
      return {
        ...state,
        result: { question: '出错了!' }
      };
    }
  },
  [CLEAN_RESULT]: (state, payload) => {
    return {
      ...state,
      question: '',
      result: {}
    };
  }
});



