import * as React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router'

import { actionGenerator } from '../constants/actionType'
import { actions, actionType } from '../utils/redux-decorators'

import Nav, { NavProps } from '../components/Nav'
import DateSelector from '../components/DateSelector/DateSelector'
import Button from '../components/Button'

@actions
class HoroscopeSelectorActions {
  static SET_BIRTHDAY = actionGenerator('SET_BIRTHDAY')
}

import './HoroscopeSelectorService'
import './horoscope-selector.scss'

type Horoscope = {
  name: string
  date: string
}

export type HoroscopeSelectorProps = {
  profile;
  dispatch;
  router;
}

export type HoroscopeSelectorPropState = {
  currentHoroscope: Horoscope | null
}

class HoroscopeSelector extends React.Component<HoroscopeSelectorProps, HoroscopeSelectorPropState> {
  @actionType()
  static SET_BIRTHDAY

  constructor(props: HoroscopeSelectorProps) {
    super(props)

    this.state = {
      currentHoroscope: {
        name: '',
        date: ''
      }
    }

    this.onSelectDate = this.onSelectDate.bind(this)
    this.setBirthday = this.setBirthday.bind(this)
    this.jump = this.jump.bind(this);
  }

  onSelectDate(year, month, date) {
    this.setState({
      currentHoroscope: {
        name: this.getHoroScopeByDate(month, date),
        date: `${year}-${month}-${date}`
      }
    })
  }

  getHoroScopeByDate(month, date) {
    let monthDateStr = `${month}.${date}`
    if ((monthDateStr >= '01.01' && monthDateStr <= '01.19') || (monthDateStr >= '12.22' && monthDateStr <= '12.31')) {
      return '摩羯座'
    } else if (monthDateStr >= '01.20' && monthDateStr <= '02.18') {
      return '水瓶座'
    } else if (monthDateStr >= '02.19' && monthDateStr <= '03.20') {
      return '双鱼座'
    } else if (monthDateStr >= '03.21' && monthDateStr <= '04.19') {
      return '白羊座'
    } else if (monthDateStr >= '04.20' && monthDateStr <= '05.20') {
      return '金牛座'
    } else if (monthDateStr >= '05.21' && monthDateStr <= '06.21') {
      return '双子座'
    } else if (monthDateStr >= '06.22' && monthDateStr <= '07.22') {
      return '巨蟹座'
    } else if (monthDateStr >= '07.23' && monthDateStr <= '08.22') {
      return '狮子座'
    } else if (monthDateStr >= '08.23' && monthDateStr <= '09.22') {
      return '处女座'
    } else if (monthDateStr >= '09.23' && monthDateStr <= '10.23') {
      return '天秤座'
    } else if (monthDateStr >= '10.24' && monthDateStr <= '11.22') {
      return '天蝎座'
    } else if (monthDateStr >= '11.23' && monthDateStr <= '12.21') {
      return '射手座'
    } else {
      return ''
    }
  }

  setBirthday() {
    this.props.dispatch({
      type: HoroscopeSelector.SET_BIRTHDAY.ACTION,
      payload: {
        birthday: this.state.currentHoroscope.date,
        callback: () => this.jump(),
        session: this.props.profile.session
      }
    })
  }

  jump() {
    const { router } = this.props;

    router.push('/today')
  }

  render() {
    return <div className="horoscope-selector">
      <p className="horoscope-tip">为了占卜更准确，请选择您的星座</p>
      <p className="currentHoroscope">当前选择为： <span
        className="currentHoroscopeName">{this.state.currentHoroscope &&
      this.state.currentHoroscope.name}</span>
      </p>
      <DateSelector onSelectDate={this.onSelectDate}></DateSelector>
      <div className="start-now">
        <Button theme="green-ml" size="medium-large" text="立即开始"
                onClick={this.setBirthday}></Button>
      </div>
    </div>
  }
}

const mapStateToProps = (state) => {
  return {
    profile: state.profile
  }
}

export default connect(mapStateToProps)(HoroscopeSelector)



