import * as React from 'react';
import { connect } from 'react-redux';
import { toast } from '../../components/Notification';
import * as UserActions from '../../actions/user';

export default function(WrappedComponent) {
  interface WithUpdateProps {
    profile;
    dispatch;
    router;
  }

  class WithUpdate extends React.Component<WithUpdateProps> {
    constructor(props: WithUpdateProps) {
      super(props);
      this.update = this.update.bind(this);
    }

    update(profile, callback) {
      const { router } = this.props;

      this
        .props
        .dispatch(
          UserActions
            .updateSelfProfile(
              this.props.profile.session,
              profile,
              () => {
                toast('更新成功');
                if(callback) {
                  callback();
                } else {
                  router.push('/account');
                }
              }
            )
        );
    }

    render() {
      const props = {
        ...this.props,
        update: this.update
      };
      return <WrappedComponent {...props}/>;
    }
  }

  const mapStateToProps = (state) => {
    return {
      profile: state.profile
    };
  };

  return connect(mapStateToProps)(WithUpdate);
}



