import * as xintu from './assets/icon-xintu@2x.png';
import * as mofawu from './assets/icon-mofawu@2x.png';

class AccountAbout extends React.Component<any, any> {
  render() {
    return (
      <Container className="account-about">
        <div className="account-about-company">
          <img src={mosuan}/>
          <h2>上海魔算信息科技有限公司</h2>
          <p>
            上海魔算信息科技有限公司是一家外资独资的科技公司，成立于2017年4月24日，公司位于上海市黄浦区，旨在为客户提供方便，
            便捷，精准的数据信息服务，并提供完整的科技解决方案。
          </p>
        </div>
        <div className="account-about-apps">
          <p>旗下代表APP</p>
          <ul>
            <li>
              <img src={xintu}/>
              <p>信徒</p>
            </li>
            <li>
              <img src={mofawu}/>
              <p>魔法屋</p>
            </li>
          </ul>
        </div>
      </Container>
    );
  }
}

export default AccountAbout;



