import * as React from 'react';
import { connect } from 'react-redux';

import delay from '../../utils/delay';
import CardHeaps from './CardHeaps';
import CardSelection from './CardSelection';

import { storyResult as getStoryResult } from '../../actions/story';

import './shuffle.scss';

export interface ShuffleProps {
  router: any;
  story: Object;
  getStoryResult: Function;
  profile: Object;
};

class Shuffle extends React.Component<ShuffleProps, any> {
  private cardHeapsRef: CardHeaps;
  private selectionRef: CardSelection;

  constructor(props: ShuffleProps) {
    super(props);

    this.state = {
      questionId: null
    };
  }

  componentDidMount() {
    this.play();
  }

  private play = async () => {
    const { cardHeapsRef, selectionRef } = this;

    await cardHeapsRef.play();

    await selectionRef.playInit();
  }

  /**
   * @desc
   * 所有动画播放完成之后回调
   */
  private onSelected = () => {
    const { router } = this.props;
    const { questionId } = this.state;

    router.push(`/result/story/${questionId}`);
  }

  /**
   * @desc
   * 播放结束动画时调用,
   * 用来请求接口
   */
  private onSelectAnimationEnd = async (selected) => {
    const getStorySuccess = (resolve) => (result) => {
      this.setState({
        questionId: result.question_id
      });

      resolve(result);
    };

    const { story, profile, getStoryResult } = this.props;
    const {
      mode,
      cardsCount,
      formation,
      questionType: divine_type,
      question,
      isPrivate
    } = story as any;
    const { session } = profile as any;

    /**
     * @todo
     * mode === easy || advanced
     *  调用相应的占卜接口 
     */
    await new Promise((resolve) => getStoryResult(
      session,
      {
        cards_array: selected.join(','),
        formation: formation && formation.name,
        divine_type: divine_type && divine_type.name,
        question,
        private: isPrivate && 'yes' || 'no'
      },
      getStorySuccess(resolve)
    ));
  }

  render() {
    const { getStoryResult, story } = this.props;
    const { cardsCount } = story as any;

    return (
      <div className="shuffle">
        <CardHeaps ref={ (node) => this.cardHeapsRef = node } />
        <CardSelection
          maxSelectNum={ cardsCount || 3 }
          ref={ (node) => this.selectionRef = node }
          onSelected={ this.onSelected }
          animEndAction={ this.onSelectAnimationEnd }
          />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    story: state.story,
    profile: state.profile
  };
};

export default connect(
  mapStateToProps,
  { getStoryResult }
)(Shuffle);


