import * as Fetch from '../utils/fetch';
import { GET_SELF_PROFILE, UPDATE_SELF_PROFILE, CHECK_LOGIN } from '../constants/api';
import { encode, decode } from '../utils/address';

const UserService = {
  getSelfProfile(session) {
    return Fetch
      .postForm(GET_SELF_PROFILE, {session, command: 1019})
      .then((data) => ({
        ...data,
        address: decode(data.address)
      }));
  },
  updateSelfProfile(session, profile) {
    return Fetch
      .postForm(UPDATE_SELF_PROFILE, {
        session,
        command: 1017,
        ...profile,
        address: encode(profile.address)
      });
  },
  checkLogin(session) {
    return Fetch
      .postForm(CHECK_LOGIN, {
        session,
        command: 1037
      })
    ;
  }
};

export default UserService;



