import * as React from 'react';
import * as _ from 'lodash';

import DiceItem from '../../components/FormationItem/index';

interface DiceProps {
  type: string;
  desc?: string;
  index?: number;
};

interface DiceState {};

class Dice extends React.Component<DiceProps, DiceState> {
  constructor(props: DiceProps) {
    super(props);
  }

  render() {
    const { type, desc, index } = this.props;

    return (
      <div className="dice-wrapper">
        <DiceItem
          className="dice"
          selected={ false }
          formation={{ id: _.isNumber(index) && `${type}-${index+1}` || type }} 
          size="mid"
          />
        { desc &&  <div className="desc">{ desc }</div> }
      </div>
    );
  }
}

export default Dice;


