
export function decode(address: string) {
  let data = {
    name: '',
    tel: '',
    location: ''
  };

  try {
    data = JSON.parse(address || '{}');
  } catch (e) {
    data.location = address;
  }

  return data;
}

export function encode(address: object) {
  return JSON.stringify(address);
}

export function format({ name, location }) {
  return [name, location].join(' ');
}



