import * as React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import Container from '../';
import Nav from '../../../components/Nav';
import Avatar from '../../../components/Avatar';
import FormItem from '../../../components/FormItem';
import { ROUTE_ROOT } from '../../../constants/constant';
import { toast } from '../../../components/Notification';
import { format } from '../../../utils/address';

import './accountProfile.scss';

interface AccountProfileProps {
  profile;
  dispatch;
  router;
}

class AccountProfile extends React.Component<AccountProfileProps, any> {
  logout() {
    localStorage.removeItem('TAROT_SESSION');
    localStorage.removeItem('IS_NEW');

    this.props.router.push(`login`);
  }

  clearCache() {
    localStorage.removeItem('IS_NEW');

    window.location.href = `UniWebView://clear`;

    toast('清除成功');
  }

  render() {
    const { profile } = this.props;
    const { nickname, face_url, birthday, coin, address } = profile;
    const navProps = {
      left: {
        type: 'back',
        to: 'today'
      },
      title: '个人中心'
    };

    return (
      <Container className="account-profile">
        <Avatar src={face_url}/>
        <p className="account-profile-nickname">{ nickname }</p>
        <p className="account-profile-coin">魔法币：{ coin }</p>
        <div className="account-profile-details">
          <Link to="account/nickname">
            <FormItem label="我的昵称">{ nickname }</FormItem>
          </Link>
          {/*<Link to="account/birthday">
            <FormItem label="我的生日">{ birthday }</FormItem>
          </Link>*/}
          <Link to="account/address">
            <FormItem label="收货地址">{ format(address) }</FormItem>
          </Link>
          <Link to="community/self">
            <FormItem label="我的占卜"/>
          </Link>
          <Link to="account/about"><FormItem label="关于我们"/></Link>
          <FormItem label="清除缓存" onClick={ () => this.clearCache() }/>
          <FormItem label="退出登录" onClick={ () => this.logout() }/>
        </div>
      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    profile: state.profile
  };
};

export default connect(mapStateToProps)(AccountProfile);



