import * as React from 'react';
import delay from '../../../utils/delay';
const moment = require('moment');

import Button from '../../../components/Button';
import CalendarItem from '../../../components/Calendar/CalendarItem';

import './todayTarotThumb.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示
export enum todayTarotThumbStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE
}

interface TodayTarotThumbProps {
  status: todayTarotThumbStatus;
  todayTarot;
}

interface TodayTarotThumbState {
  status: todayTarotThumbStatus;
}

export class TodayTarotThumb extends React.Component<TodayTarotThumbProps, TodayTarotThumbState> {
  constructor(props: TodayTarotThumbProps) {
    super(props);
    this.state = {
      status: todayTarotThumbStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  async componentDidMount() {
    this.changeStatus([0, todayTarotThumbStatus.MOUNTED]);
  }

  getClassByStatus(status) {
    return status !== todayTarotThumbStatus.HIDDEN && status !== todayTarotThumbStatus.MOUNTED ? 'visible' : '';
  }

  render() {
    const { status } = this.state;
    const { todayTarot } = this.props;
    if (status === todayTarotThumbStatus.HIDDEN) {
      return null;
    }
    const [day, date] = [moment().day(), moment().date()];
    const position = [1037, 59, 222, 385, 548, 711, 874];
    const left = window['lib'].flexible.px2rem(position[day]);
    return (
      <div className={`today-tarot-thumb ${this.getClassByStatus(status)}`} style={{left}}>
        <CalendarItem date={date} highlight={true} tarot={todayTarot.today_tarot} />
      </div>
    );
  }
}



