import * as React from 'react';

interface NotFoundProps {}
interface NotFoundState {}

class NotFound extends React.Component<NotFoundProps, NotFoundState> {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="404">
        Not found
      </div>
    );
  }
}

export default NotFound;



