import * as React from 'react';
import * as _ from 'lodash';
import { connect } from 'react-redux';
import Slider from 'react-slick';

import Nav from '../../components/Nav';
import Button from '../../components/Button';

import { storyCardsCount } from '../../actions/story';

import './index.scss';

interface NewbieProps { router, dispatch: Function }
interface NewbieState {}
interface NewbieQuestions {
  title: string;
  questions: Array<string>;
  cardsAmount: number;
}

const QUESTIONS: Array<NewbieQuestions> = [
  {
    cardsAmount: 1,
    title: '根据牌本身的意义来解释所问问题。',
    questions: ['他喜欢我吗？', '我能应聘上这个工作吗？', '我适合炒股吗？']
  },
  {
    cardsAmount: 3,
    title: '三张牌综合分析，让自己明白下一步该如何做。',
    questions: ['我能去创业吗？', '我们的感情会怎样发展？', '我有没有必要去健身？']
  },
  {
    cardsAmount: 4,
    title: '多用于是非选择题。',
    questions: ['我要不要辞职？', '我会不会被裁掉？', '我要不要先开口表白？']
  },
  {
    cardsAmount: 5,
    title: '感情发展走势以及事业抉择方面等。',
    questions: ['我应该选择哪个职业？', '我未来5周的运势怎么样？', '我感情中的问题在哪？']
  },
  {
    cardsAmount: 6,
    title: '对当下情况的描述，而对你的行为做出指导。',
    questions: ['我们能不能复合？', '这个投资项目有问题吗？', '我为什么老是减肥不成功？']
  },
  {
    cardsAmount: 7,
    title: '把事情的来龙去脉了解清楚，在此基础上选择出一个好的解决方法。',
    questions: ['公司的经营状况如何？', '我为什么老是遇不到对的人？', '我最近为什么那么倒霉？']
  }
]

class Step1 extends React.Component<NewbieProps, NewbieState> {
  state = {
    slideIndex: 0,
  }

  onNextClick = (cardsCount) => {
    const { dispatch, router } = this.props;

    dispatch(storyCardsCount(cardsCount));

    router.push({
      pathname: '/question/type/easy',
      query: { cardsCount },
    })
  }

  render() {
    const { slideIndex } = this.state;
    const { router } = this.props;

    return (
      <div className="question-type">
        <div className="slide-wrapper">
          <Slider
            centerMode
            infinite={false}
            afterChange={index => this.setState({ slideIndex: index })}
            centerPadding="100px"
            slidesToShow={1}
            arrows={false}
            prevArrow={null}
            nextArrow={null}
          >
            {
              _.range(1, 8).map((value) => {
                if(value === 2) return null;

                return (
                  <div key={value} className="num-select">
                    <img className={`img-${value}`} />
                    <p className="text-align-center num-indicator">{value}张牌</p>
                  </div>
                );
              })
            }
          </Slider>
        </div>
        {
          <p className="text-align-center">
            <p className="num-type">{QUESTIONS[slideIndex].title}</p>
            <p className="num-type-example-title">例如</p>
            <p className="num-type-example">{QUESTIONS[slideIndex].questions[0]}</p>
            <p className="num-type-example">{QUESTIONS[slideIndex].questions[1]}</p>
            <p className="num-type-example">{QUESTIONS[slideIndex].questions[2]}</p>
          </p>
        }
        <div className="button-wrap">
          <Button theme="brown" size="large" text="下一步"
            onClick={ () => this.onNextClick(QUESTIONS[slideIndex].cardsAmount) }
            />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps: NewbieProps) => {
  return {}
}

export default connect(mapStateToProps)(Step1);


