/**
 * 首次登录的引导页面
 * @since 2017-09-28
 * @author yuan.gao@dianrong.com
 */

import * as React from 'react';
import * as moment from 'moment';
import * as _ from 'lodash';
import * as classNames from 'classnames';
import { connect } from 'react-redux';
import delay from '../../utils/delay';
import Button from '../../components/Button';
import * as TarotActions from '../../actions/tarot';
import * as StoryActions from '../../actions/story';
import * as UserActions from '../../actions/user';
import { updateSelfProfile } from '../../actions/user';
import { setShareContent } from '../../actions/global';
import TarotService from '../../services/tarot';
import { SHARE_CONTENT } from '../../constants/constant';

import { SHARE_URL } from '../../components/Nav';
import { todayHeaderStatus, TodayHeader } from './TodayHeader';
import { todayTarotStatus, TodayTarot } from './TodayTarot';
import { todayPlayStatus, TodayPlay } from './TodayPlay';
import { todayShineStatus, TodayShine } from './TodayShine';
import { todayPlayTipStatus, TodayPlayTip } from './TodayPlayTip';
import { todayDetailStatus, TodayDetail } from './TodayDetail';
import { todayTarotThumbStatus, TodayTarotThumb } from './TodayTarotThumb';
import { todayCalendarTipStatus, TodayCalendarTip } from './TodayCalendarTip';
import { todayDetailTipStatus, TodayDetailTip } from './TodayDetailTip';
import { calendarMode, Calendar } from '../../components/Calendar';
import Avatar from '../../components/Avatar';
import { RecommandCommunity } from './RecommandCommunity';
import { toast } from '../../components/Notification';

import './today.scss';

enum todayMode {
  HIDDEN,
  VISIBLE,
  GUIDE,
  GUIDDING,
  STATIC
}

interface TodayProps {
  dispatch;
  profile;
  tarot;
  community;
  router;
  shareContent;
}

interface TodayState {
  mode: todayMode;
  headerStatus: todayHeaderStatus;
  tarotStatus: todayTarotStatus;
  playStatus: todayPlayStatus;
  playTipStatus: todayPlayTipStatus;
  shineStatus: todayShineStatus;
  detailStatus: todayDetailStatus;
  tarotThumbStatus: todayTarotThumbStatus;
  calendarTipStatus: todayCalendarTipStatus;
  detailTipStatus: todayDetailTipStatus;
  todayCalendarMode: calendarMode;
  showStoryLink: boolean;
}

class Today extends React.Component<TodayProps, TodayState> {
  scrollHandlerId = null;

  checkedStatus = {
    mode: todayMode.STATIC,
    tarotStatus: todayTarotStatus.STATIC,
    headerStatus: todayHeaderStatus.STATIC,
    detailStatus: todayDetailStatus.STATIC,
    playStatus: todayPlayStatus.HIDDEN
  };

  uncheckStatus = {
    mode: todayMode.STATIC,
    tarotStatus: todayTarotStatus.VISIBLE,
    headerStatus: todayHeaderStatus.STATIC,
    detailStatus: todayDetailStatus.HIDDEN,
    playStatus: todayPlayStatus.VISIBLE
  };

  constructor(props: TodayProps) {
    super(props);

    this.state = {
      mode: todayMode.VISIBLE,
      headerStatus: todayHeaderStatus.HIDDEN,
      tarotStatus: todayTarotStatus.HIDDEN,
      playStatus: todayPlayStatus.HIDDEN,
      playTipStatus: todayPlayTipStatus.HIDDEN,
      shineStatus: todayShineStatus.HIDDEN,
      detailStatus: todayDetailStatus.HIDDEN,
      tarotThumbStatus: todayTarotThumbStatus.HIDDEN,
      calendarTipStatus: todayCalendarTipStatus.HIDDEN,
      detailTipStatus: todayDetailTipStatus.HIDDEN,
      todayCalendarMode: calendarMode.BOTH_SINGLE,
      showStoryLink: false
    };

    this.play = this.play.bind(this);
    this.signIn = this.signIn.bind(this);
    this.scrollHandler = this.scrollHandler.bind(this);
    this.setAllChecked = this.setAllChecked.bind(this);
    this.setAllUnchecked = this.setAllUnchecked.bind(this);
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        [status[1]]: status[2]
      });
    }
  }

  setAllChecked() {
    this.setState(this.checkedStatus);
  }

  setAllUnchecked() {
    this.setState(this.uncheckStatus);
  }

  startTodayMode() {
    this.setState({
      mode: todayMode.GUIDE
    }, () => {
      // 头部入场
      this.changeStatus([0, 'headerStatus', todayHeaderStatus.MOUNTED], [1000, 'headerStatus', todayHeaderStatus.VISIBLE]);
      // 塔罗牌入场
      this.changeStatus([0, 'tarotStatus', todayTarotStatus.MOUNTED], [1000, 'tarotStatus', todayTarotStatus.VISIBLE]);
      // 按钮入场
      this.changeStatus([0, 'playStatus', todayPlayStatus.MOUNTED], [1000, 'playStatus', todayPlayStatus.VISIBLE]);
      // 先挂载，2s后显示按钮提示
      this.changeStatus([0, 'playTipStatus', todayPlayTipStatus.MOUNTED], [2000, 'playTipStatus', todayPlayTipStatus.VISIBLE]);
    });
  }

  run() {
    const { tarot, profile } = this.props;
    if (profile.isNew) {
      this.startTodayMode();
    } else if (this.props.tarot.today_ischeck) {
      this.setAllChecked();
    } else {
      this.setAllUnchecked();
    }
  }

  componentDidMount() {
    this.scrollHandlerId = this.scrollHandler();

    const { tarot } = this.props;

    const { today_ischeck } = tarot; 

    this.setShareContent(tarot);

    if(_.isBoolean(today_ischeck)) {
      return this.run();
    }

    this.props.dispatch(TarotActions.getHistory(this.props.profile.session, this.run.bind(this)));
  }

  componentWillReceiveProps(nextProps) {
    const { dispatch, tarot } = this.props;

    if(!_.isEqual(nextProps.tarot, tarot)) {
      this.setShareContent(nextProps.tarot);
    }
  }

  componentWillUnmount() {
    clearInterval(this.scrollHandlerId);
  }

  setShareContent = (tarot) => {
    const { dispatch } = this.props;
    const content = tarot.tarot_text || '###';
    const [name, position, divine, text] = content.split('#');
    const shareText = text && `占卜结果: ${text}` ||
      '塔罗牌实在太准了！快来寻找属于你的答案！';

    dispatch(setShareContent({
      title: SHARE_CONTENT.title[divine] || '魔法屋-口袋里的占卜神器',
      text: shareText,
      icon: SHARE_CONTENT.icon,
      link: SHARE_CONTENT.url,
      comment: `今日运势`,
      channel: 'wechat',
      type: 'webpage'
    }));
  }

  getClassByStatus(status) {
    switch (status) {
    case todayMode.HIDDEN:
      return '';
    case todayMode.VISIBLE:
      return 'visible';
    case todayMode.GUIDE:
      return 'guide';
    case todayMode.GUIDDING:
      return 'guidding';
    case todayMode.STATIC:
      return 'static';
    default:
      return '';
    }
  }

  async play() {
    const { profile, router, dispatch } = this.props;

    /**
     * @desc
     * 更新isnewplayer状态
     * localstorage: IS_NEW -> 0,
     * reducer: isNew ->  0
     */
    localStorage.setItem('IS_NEW', 'false');

    this.props.dispatch(UserActions.setIsNotNew());

    // 按钮提示消失
    this.changeStatus([0, 'playTipStatus', todayPlayTipStatus.MOUNTED], [1000, 'playTipStatus', todayPlayTipStatus.HIDDEN]);
    // 按钮消失
    this.changeStatus([500, 'playStatus', todayPlayStatus.MOUNTED], [3500, 'playStatus', todayPlayStatus.HIDDEN]);
    // 下沉
    await delay(1000);
    this.setState({
      mode: todayMode.GUIDDING
    });
    // 背景闪光出现，转2s，然后消失
    this.changeStatus([0, 'shineStatus', todayShineStatus.MOUNTED], [2000, 'shineStatus', todayShineStatus.VISIBLE], [2000, 'shineStatus', todayShineStatus.MOUNTED], [2000, 'shineStatus', todayShineStatus.HIDDEN]);
    // 闪光消失后, 翻牌
    this.changeStatus([5000, 'tarotStatus', todayTarotStatus.GUIDE_FLIPPING]);
    // 翻牌后，出现detail
    this.changeStatus([5500, 'detailStatus', todayDetailStatus.MOUNTED], [500, 'detailStatus', todayDetailStatus.GUIDE_VISIBLE]);
    // detail出现同时，缩小塔罗牌落下
    this.changeStatus([7000, 'tarotThumbStatus', todayTarotThumbStatus.MOUNTED], [1000, 'tarotThumbStatus', todayTarotThumbStatus.VISIBLE]);
    // 塔罗落下后，显示日历提示，显示2s，然后消失
    this.changeStatus(
      [10000, 'calendarTipStatus', todayCalendarTipStatus.MOUNTED],
      [1000, 'calendarTipStatus', todayCalendarTipStatus.VISIBLE],
      [2000, 'calendarTipStatus', todayCalendarTipStatus.MOUNTED],
      [2000, 'calendarTipStatus', todayCalendarTipStatus.HIDDEN]
    );
    // 显示日历提示消失，显示详情提示，显示2s，然后消失
    this.changeStatus(
      [14000, 'detailTipStatus', todayDetailTipStatus.MOUNTED],
      [1000, 'detailTipStatus', todayDetailTipStatus.VISIBLE],
      [2000, 'detailTipStatus', todayDetailTipStatus.MOUNTED],
      [2000, 'detailTipStatus', todayDetailTipStatus.HIDDEN]
    );
    await delay(20000);
    this.setAllChecked();
    // 隐藏掉小图
    this.changeStatus([20000, 'tarotThumbStatus', todayTarotThumbStatus.HIDDEN]);

    router.push({
      path: '',
      search: `?session=${profile.session}&isnewplayer=0`
    });
  }

  async signIn() {
    this.props.dispatch(TarotActions.signIn(this.props.profile.session, () => {
      this.props.profile.isNew ? this.play() : this.setAllChecked();
    }));
  }

  scrollHandler = () => {
    const { container } = this.refs;

    return setInterval(() => {
      const { todayCalendarMode, showStoryLink } = this.state;
      const scrollTop = (container as any).scrollTop;
      const nextState = {
        todayCalendarMode,
        showStoryLink
      };

      nextState.todayCalendarMode = scrollTop < 40 && calendarMode.BOTH_SINGLE || calendarMode.BOTH_FULL;
      nextState.showStoryLink = scrollTop >= 40;
      !_.isEqual({ todayCalendarMode, showStoryLink }, nextState) &&
        this.setState(nextState);
    }, 100);
  }

  transitionTo = (to) => {
    const { router } = this.props;
    router.push(to);
  }

  onCalendarItemClick = (tarotNo, date) => {
    const { mode } = this.state;

    if(mode === todayMode.GUIDDING || mode === todayMode.GUIDE) return;

    const { dispatch, profile, router } = this.props;
    const { session } = profile;
    date == moment().date() ?
      router.push(`result/today/${tarotNo}`) :
      router.push(`result/daily/${tarotNo}/${moment().date(date).format('YYYY年M月D日运势')}`)
    ;
  }

  onShare = () => {
    const { shareContent } = this.props;

    window.location.href = SHARE_URL(shareContent);
  }

  render() {
    const { profile, tarot, community, router } = this.props;
    const {
      mode,
      headerStatus, tarotStatus, playStatus, playTipStatus,
      shineStatus, detailStatus, tarotThumbStatus, calendarTipStatus,
      detailTipStatus, todayCalendarMode, showStoryLink
    } = this.state;

    return (
      <div ref="container" className={`user-today ${this.getClassByStatus(mode)} ${todayCalendarMode === calendarMode.FULL ? 'full' : ''}`}>
        <div className="user-today-content">
          <div className="today-share" onClick={ this.onShare } />

          <div className="today-avatar">
            <Avatar src={profile.face_url} onClick={ () => this.transitionTo(`/account`) } />
          </div>
          <div className="today-mask" />
          {
            mode === todayMode.STATIC ? null : (
              <div>
                <TodayShine status={shineStatus} />
                <TodayPlayTip text="点击可抽取<br/>今日塔罗运势" status={playTipStatus}  />
                <TodayDetailTip text="点击<br/>查看详情哦" status={detailTipStatus}  />
                <TodayCalendarTip text="每日可以抽取<br/>一次，并显示在此" status={calendarTipStatus}  />
              </div>
            )
          }
          <TodayHeader status={headerStatus} profile={profile} />
          <div className="middle-content">
            <TodayTarot
              onClick={ () => tarot.today_ischeck && this.onCalendarItemClick(tarot.today_tarot, moment().date()) }
              status={tarotStatus}
              todayTarot={tarot}
              />
            <TodayDetail
              onClick={ () => this.onCalendarItemClick(tarot.today_tarot, moment().date()) }
              status={detailStatus}
              todayTarot={tarot}
            />
            <TodayPlay status={playStatus} onClick={this.signIn} />
          </div>
          <TodayTarotThumb status={tarotThumbStatus} todayTarot={tarot} />
          <div className={`today-calendar-container ${todayCalendarMode === calendarMode.BOTH_FULL ? 'full' : '' }`}>
            <p className="calendar-tip">已经连续抽取了 <b>{tarot.checkcount}</b> 日，连续抽取一个月，可以兑换礼物哦</p>
            <header style={{display: todayCalendarMode === calendarMode.BOTH_FULL ? 'none' : 'block'}}>本周塔罗运势</header>
            <a className="today-calendar-gift" />
            <div className="today-calendar">
              <Calendar onItemClick={ (tarot, date) => { this.onCalendarItemClick(tarot, date) } } mode={todayCalendarMode} tarots={tarot.checklist} />
            </div>
          </div>
          <RecommandCommunity router={router} dispatch={this.props.dispatch} community={community.recommand} profile={this.props.profile} pagination={community.pagination.recommand} />
        </div>
        <div className={ classNames(["story-link", { hide: !showStoryLink }, { show: showStoryLink }]) }>
          <Button theme="green-long" size="large" text="塔罗占卜" onClick={this.transitionTo.bind(this, '/mode/select')} />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    profile: state.profile,
    tarot: state.tarot,
    community: state.community,
    shareContent: state.global.shareContent
  };
};

export default connect(mapStateToProps)(Today);



