import {
  STORY_MODE_SELECT,
  STORY_CARDS_COUNT_SELECT,
  STORY_QUESTION_TYPE_SELECT,
  STORY_QUESTION_INPUT,
  STORY_FORMATION_SELECT,
  STORY_PRIVATE,
  STORY_GET_RESULT,
  GET_DAILY_DIVINE,
  GET_RESULT_BY_ID,
  CLEAN_RESULT
} from '../constants/actionType';

export function storyModeSelect(mode, callback) {
  return {
    type: STORY_MODE_SELECT.ACTION,
    payload: {
      mode,
      callback
    }
  };
}

export function storyCardsCount(cardsCount) {
  return {
    type: STORY_CARDS_COUNT_SELECT,
    payload: {
      cardsCount
    }
  };
}

export function storyQuestionTypeSelect(type, callback) {
  return {
    type: STORY_QUESTION_TYPE_SELECT.ACTION,
    payload: {
      type,
      callback
    }
  };
}

export function storyQuestionInput(input, callback) {
  return {
    type: STORY_QUESTION_INPUT.ACTION,
    payload: {
      input,
      callback
    }
  };
}

export function storyFormationSelect(formation, callback) {
  return {
    type: STORY_FORMATION_SELECT.ACTION,
    payload: {
      formation,
      callback
    }
  };
}

export function storyPrivate(isPrivate, callback) {
  return {
    type: STORY_PRIVATE.ACTION,
    payload: {
      isPrivate,
      callback
    }
  };
}

/**
 * @deprecated use storyResultById
 */
export function storyResult(session, story, callback) {
  return {
    type: STORY_GET_RESULT.ACTION,
    payload: {
      session,
      story,
      callback
    }
  };
}

export function getDailyDivine(session, cardNo, callback) {
  return {
    type: GET_DAILY_DIVINE.ACTION,
    payload: {
      session,
      card: cardNo,
      callback
    }
  };
}

export function storyResultById(session, id, callback) {
  return {
    type: GET_RESULT_BY_ID.ACTION,
    payload: {
      session,
      id,
      callback
    }
  };
}

export function cleanResult() {
  return {
    type: CLEAN_RESULT
  }
}



