import 'isomorphic-fetch';
import * as param from 'jquery-param';

const credentials: any = 'include';

function checkStatus(response) {
  if (response.status >= 200 && response.status < 300) {
    return response;
  }

  const error = new Error(response.statusText);
  throw error;
}

function parseJSON(response) {
  return response.json().then((resJson) => resJson);
}

export function post(url, params) {
  const config = {
    method: 'post',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    credentials,
    body: JSON.stringify(params)
  };

  return fetch(url, config).then(checkStatus).then(parseJSON);
}

export function postForm(url, params) {
  const config = {
    method: 'post',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    credentials,
    body: param(params)
  };

  return fetch(url, config).then(checkStatus).then(parseJSON);
}

export function get(url, query = {}) {
  const config = {
    method: 'get',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    credentials
  };
  query = param(query);
  return fetch(`${url}${query ? '?' + query : ''}`, config).then(checkStatus).then(parseJSON);
}



