export function actionGenerator(key) {
  return {
    ACTION: key,
    SUCCESS: `${key}_SUCCESS`,
    FAILED: `${key}_FAILED`
  };
}

// 修改nav设置
export const SET_NAV_CONFIG = 'SET_NAV_CONFIG';

// 修改分享内容
export const SET_SHARE_CONTENT = 'SET_SHARE_CONTENT';

// 修改背景
