// 设置是否是新用户
export const SET_NEW_USER_STATE = 'SET_NEW_USER_STATE';

// 获取当前用户信息
export const GET_SELF_PROFILE = actionGenerator('GET_SELF_PROFILE');
// 更新当前用户信息
export const UPDATE_SELF_PROFILE = actionGenerator('UPDATE_SELF_PROFILE');

// 签到
export const SIGN_IN = actionGenerator('SIGN_IN');

