import * as React from 'react';
import delay from '../../../utils/delay';

import Tarot from '../../../components/Tarot';

import './todayTarot.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示, GUIDE_PLAYING: 开始抽牌下移, FLIPPING: 翻转, GUIDE_FLIPPING: 当行状态下翻转
export enum todayTarotStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE,
  FLIPPING,
  STATIC,
  GUIDE_PLAYING,
  GUIDE_FLIPPING,
}

interface TodayTarotProps {
  status: todayTarotStatus;
  todayTarot;
  onClick?;
}

interface TodayTarotState {
  status: todayTarotStatus;
}

export class TodayTarot extends React.Component<TodayTarotProps, TodayTarotState> {
  constructor(props: TodayTarotProps) {
    super(props);
    this.state = {
      status: todayTarotStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  componentDidMount() {
  }

  getClassByStatus(status) {
    switch (status) {
    case todayTarotStatus.HIDDEN:
    case todayTarotStatus.MOUNTED:
      return '';
    case todayTarotStatus.VISIBLE:
      return 'visible';
    case todayTarotStatus.GUIDE_PLAYING:
      return 'guide-playing';
    case todayTarotStatus.GUIDE_FLIPPING:
      return 'guide-flipping';
    case todayTarotStatus.FLIPPING:
      return 'flipping';
    case todayTarotStatus.STATIC:
      return 'flipping static';
    default:
      return '';
    }
  }

  render() {
    const { todayTarot, onClick } = this.props;
    const { status } = this.state;

    // if (status === todayTarotStatus.HIDDEN) {
    //   return null;
    // }
    return (
      <div onClick={ onClick } className={`today-tarot ${this.getClassByStatus(status)}`}>
        <h2>今日塔罗运势</h2>
        <Tarot size="large" frontImgSrc={todayTarot.today_tarot} />
      </div>
    );
  }
}



