import * as React from 'react';
import { connect } from 'react-redux';
import Button from '../../components/Button';
import * as StoryActions from '../../actions/story';
import * as QuestionActions from '../../actions/question';

import './questionType.scss';

interface QuestionTypeProps {
  dispatch;
  profile;
  story;
  location;
  router;
  params;
}

interface QuestionTypeState {}

class QuestionType extends React.Component<QuestionTypeProps, QuestionTypeState> {
  constructor(props: QuestionTypeProps) {
    super(props);
  }

  transitionTo = () => {
    const { 
      dispatch,
      location: { query: { cardsCount } },
      story: { questionType, formation },
      router,
      params
    } = this.props;

    const { mode } = params;

    if (cardsCount) {
      dispatch(QuestionActions.newbieQuestionResult({ cardsCount, divineType: questionType.name }, () => {}));
    } else {
      dispatch(QuestionActions.questionResult({ formation: formation.name, divineType: questionType.name }, () => {}));      
    }

    router.push(`/question/input/${mode || ''}`);
  }

  selectQuestionType(questionType) {
    this.props.dispatch(StoryActions.storyQuestionTypeSelect(questionType, () => {}));
  }

  render() {
    const { story, params } = this.props;
    const { mode } = params;

    return (
      <div className="question-type">
        <div className="question-type-wrap">
          {
            this.props.story.questionTypes.map((questionType, index) => {
              return (
                <div key={index} className={`question-type-item ${story.questionType === questionType ? 'selected' : ''}`} onClick={this.selectQuestionType.bind(this, questionType)}>
                  <p>{questionType.name}</p>
                </div>
              );
            })
          }
        </div>
        <div className="button-wrap">
          <Button 
            size="large"
            theme={ mode === 'easy' && 'orange' || 'blue' }
            text="下一步"
            onClick={this.transitionTo}
            />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    profile: state.profile,
    story: state.story,
  };
};

export default connect(mapStateToProps)(QuestionType);



