import * as React from 'react';
import * as classNames from 'classnames';
import delay from '../../../utils/delay';

import Heap, { HEAP_MODE } from './heap';
import seqSetState from '../seqSetState';

import './cardHeaps.scss';

const WRAPPER_MODE = {
  MOVE: 'move',
};

export interface CardHeapsProps {};

interface CardHeapsState {
  wrapperMode: Array<string>,
  midHeapMode: Array<string>,
  leftHeapMode: Array<string>,
  rightHeapMode: Array<string>
};

class CardHeaps extends React.Component<CardHeapsProps, CardHeapsState> {
  constructor(props: CardHeapsProps) {
    super(props);

    this.state = {
      wrapperMode: [],
      midHeapMode: [],
      leftHeapMode: [],
      rightHeapMode: []
    };

    this.play = this.play.bind(this);
  }

  componentDidMount() {
  }

  private animations = [
    { duration: 500 },
    { duration: 600, states: [{ midHeapMode: [HEAP_MODE.APPEAR] }] },
    { duration: 100 },
    {
      duration: 200,
      states: [{
        leftHeapMode: [HEAP_MODE.APPEAR, HEAP_MODE.TOLEFT],
        rightHeapMode: [HEAP_MODE.APPEAR, HEAP_MODE.TORIGHT]
      }]
    },
    { duration: 200 },
    { 
      duration: 1000,
      states: [{
        wrapperMode: [HEAP_MODE.MOVE],
      }]
    },
    { duration: 200 },
    {
      duration: 200,
      states: [{
        wrapperMode: [],
        leftHeapMode: [HEAP_MODE.TOMID, HEAP_MODE.DISAPPEAR],
        rightHeapMode: [HEAP_MODE.TOMID, HEAP_MODE.DISAPPEAR]
      }]
    },
    { duration: 100 },
    { duration: 600, states: [{ midHeapMode: HEAP_MODE.DISAPPEAR }]},
    { duration: 500 }
  ];

  private repeater = (mode) => {
    const repeater = [];

    for(let i = 1; i <= 6; i++) {
      repeater.push(<Heap key={ i } style={{ zIndex: 3 }} heapModes={ mode } className={ `repeater-${i}` } />)
    }

    return repeater;
  }

  public async play() {
    await seqSetState(this.animations, (states) => this.setState(states));
  }

  render() {
    const {
      wrapperMode,
      midHeapMode,
      leftHeapMode,
      rightHeapMode
    } = this.state;

    return (
      <div className="card-heap-wrapper">
        <div className={ classNames("card-heaps", wrapperMode) }>
          <Heap style={{ zIndex: 3 }} heapModes={ midHeapMode } />
          <Heap style={{ zIndex: 2 }} heapModes={ leftHeapMode } />
          <Heap style={{ zIndex: 2 }} heapModes={ rightHeapMode } />
          { this.repeater(midHeapMode) }
        </div>
     </div>
    );
  }
}

export default CardHeaps;



