import * as Fetch from '../utils/fetch';
import { GET_STORY_RESULT, GET_DAILY_DIVINE, GET_RESULT_BY_ID } from '../constants/api';

const StoryService = {
  storyResult(story, session) {
    return Fetch.postForm(GET_STORY_RESULT, {
      session,
      command: 1029,
      ...story
    });
  },

  dailyDivine(card, session) {
    return Fetch.postForm(GET_DAILY_DIVINE, {
      session,
      command: 1031,
      card
    });
  },

  storyResultById(id, session) {
    return Fetch.postForm(GET_RESULT_BY_ID, {
      session,
      id,
      command: 1035
    });
  }
};

export default StoryService;



