import * as Fetch from '../utils/fetch';
import { GET_COMMUNITY_LIST } from '../constants/api';

const CommunityService = {
  getList(session, pagination) {
    return Fetch.postForm(GET_COMMUNITY_LIST, {session, command: '1033', ...pagination});
  },
  getRecommendList(session, pagination) {
    return Fetch.postForm(GET_COMMUNITY_LIST, {session, command: '1033', ...pagination});
  }
};

export default CommunityService;



