import * as React from 'react';
import delay from '../../../utils/delay';

import './todayShine.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示
export enum todayShineStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE
}

interface TodayShineProps {
  status: todayShineStatus;
  style?: Object;
}

interface TodayShineState {
  status: todayShineStatus;
}

export class TodayShine extends React.Component<TodayShineProps, TodayShineState> {
  constructor(props: TodayShineProps) {
    super(props);
    this.state = {
      status: todayShineStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  async componentDidMount() {
    this.changeStatus([0, todayShineStatus.MOUNTED]);
  }

  getClassByStatus(status) {
    return status === todayShineStatus.VISIBLE ? 'visible' : '';
  }

  render() {
    const { style } = this.props;
    const { status } = this.state;
    if (status === todayShineStatus.HIDDEN) {
      return null;
    }
    return (
      <div style={ style } className={`today-shine ${this.getClassByStatus(status)}`} />
    );
  }
}



