/**
 * 模式选择页面
 * @since 2017-10-14
 * @author yuan.gao@dianrong.com
 */

import * as React from 'react';
import { connect } from 'react-redux';
import Button from '../../components/Button';
import * as StoryActions from '../../actions/story';

import './modeSelect.scss';

interface ModeSelectProps {
  dispatch;
  profile;
  router;
}

interface ModeSelectState {}

class ModeSelect extends React.Component<ModeSelectProps, ModeSelectState> {
  constructor(props: ModeSelectProps) {
    super(props);
    this.transitionTo = this.transitionTo.bind(this);
  }

  transitionTo(mode) {
    const { router } = this.props; 
    this.props.dispatch(StoryActions.storyModeSelect(mode, () => {
      router.push(mode === 'easy' ? '/newbie' : '/formation');
    }));
  }

  render() {
    return (
      <div className="mode-select">
        <div className="button-wrap">
          <Button size="large" theme="orange" text="新手模式" onClick={this.transitionTo.bind(this, 'easy')} />
          <p>按照指引选择 所需的张数和类型</p>
        </div>
        <div className="button-wrap">
          <Button size="large" theme="blue" text="专家模式" onClick={this.transitionTo.bind(this, 'advanced')} />
          <p>直接选择塔罗阵型</p>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    profile: state.profile
  };
};

export default connect(mapStateToProps)(ModeSelect);



