/**
 * 阵型选择
 * @since 2017-10-14
 * @author yuan.gao@dianrong.com
 */

import * as React from 'react';
import { connect } from 'react-redux';

import Button from '../../components/Button';
import { toast } from '../../components/Notification';
import * as StoryActions from '../../actions/story';

import './questionInput.scss';

interface QuestionInputProps {
  dispatch;
  profile;
  story;
  questions;
  router;
  params;
}

interface QuestionInputState {}

class QuestionInput extends React.Component<QuestionInputProps, QuestionInputState> {
  constructor(props: QuestionInputProps) {
    super(props);
    this.changePrivate = this.changePrivate.bind(this);
  }

  transitionTo = () => {
    const { story, router } = this.props;
    const { question } = story;

    if(!question) {
      toast('请输入您要问的问题');
      return;
    }

    router.push('/shuffle');
  }

  selectQuestionInput(question) {
    this.props.dispatch(StoryActions.storyQuestionInput(question, () => {}));
  }

  changePrivate(isPrivate) {
    this.props.dispatch(StoryActions.storyPrivate(isPrivate, () => {}));
  }

  render() {
    const { story, questions = [], params } = this.props;
    /**
     * @description
     * mode can be 'easy' or 'advance'
     */
    const { mode } = params;

    return (
      <div className="question-input">
        <div className="question-input-wrap">
          <input type="text" placeholder="请输入您要问的问题" onChange={(event) => this.selectQuestionInput(event.target.value)} value={story.question} />
        </div>
        <div className="private-wrap">
          <div onClick={this.changePrivate.bind(this, true)}>
            <i className={`private-check ${story.isPrivate ? 'checked' : ''}`} />
            <div className="desc">
              <p>隐私问题</p>
              <label>隐私问题将不会被公开</label>
            </div>
          </div>
          <div onClick={this.changePrivate.bind(this, false)}>
            <i className={`private-check ${story.isPrivate ? '' : 'checked'}`} />
            <div className="desc">
              <p>公开问题</p>
              <label>塔罗墙展示，获得5魔法币</label>
            </div>
            <i className="coin" />
          </div>
        </div>
        <div className="questions">
          {
            questions && questions.map((q, index) => (
              <p
                key={index}
                className={`question question-${index + 1}`}
                onClick={ () => this.selectQuestionInput(q) }
                >
                {q}
              </p>
            ))
          }
        </div>
        <div className="button-wrap">
          <Button
            size="large"
            theme={ mode === 'easy' && 'orange' || 'blue' }
            text="开始发牌"
            onClick={this.transitionTo}
            />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    profile: state.profile,
    story: state.story,
    questions: state.question.questions,
  };
};

export default connect(mapStateToProps)(QuestionInput);



