import * as React from 'react';
import delay from '../../../utils/delay';

import './todayDetail.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示
export enum todayDetailStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE,
  STATIC,
  GUIDE_VISIBLE
}

interface TodayDetailProps {
  status: todayDetailStatus;
  todayTarot;
  onClick?;
}

interface TodayDetailState {
  status: todayDetailStatus;
}

export class TodayDetail extends React.Component<TodayDetailProps, TodayDetailState> {
  constructor(props: TodayDetailProps) {
    super(props);
    this.state = {
      status: todayDetailStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  getClassByStatus(status) {
    switch (status) {
    case todayDetailStatus.HIDDEN:
    case todayDetailStatus.MOUNTED:
      return '';
    case todayDetailStatus.VISIBLE:
      return 'visible';
    case todayDetailStatus.GUIDE_VISIBLE:
      return 'today-visible';
    case todayDetailStatus.STATIC:
      return 'static';
    default:
      return '';
    }
  }

  render() {
    const { status } = this.state;
    const { todayTarot, onClick } = this.props;
    const detail = todayTarot.tarot_text.split('#');
    if (status === todayDetailStatus.HIDDEN) {
      return null;
    }
    return (
        <div onClick={ onClick } className={`today-detail ${this.getClassByStatus(status)}`}>
          <p className="today-tarot-detail">
            <label>牌面：{detail[0]}</label>
            <label>方向：{ detail[1] }</label>
          </p>
          <div className="today-tarot-explain">
            <i className="today-tarot-quality">{ detail[2] }</i>
            <p className="today-tarot-content">
              { detail[3] }
            </p>
          </div>
        </div>
    );
  }
}



