import * as React from 'react';
import Container from '../';
import Nav, { NavProps } from '../../../components/Nav';
import Button from '../../../components/Button';
import FormItem from '../../../components/FormItem';
import withUpdate from '../withUpdate';
import { validateName } from '../../../utils/validate';
import { toast } from '../../../components/Notification';

import './accountNickname.scss';

interface AccountNicknameProps {
  profile;
  update;
  params;
  router;
}

interface AccountNicknameState {
  nickname: string;
}

export default withUpdate(class AccountNickname extends React.Component<AccountNicknameProps, AccountNicknameState> {
  constructor(props: AccountNicknameProps) {
    super(props);
    this.state = {
      nickname: props.profile.nickname
    };

    this.submit = this.submit.bind(this);
  }

  /**
   * @todo
   * 暂时跳过生日设置页
   */
  submit() {
    const { nickname } = this.state;
    const { profile, router, params } = this.props;
    const { isNew } = params;
    const msg = validateName(nickname, '昵称');

    if (msg) {
      toast(msg);
      return;
    }

    if(!!isNew) {
      this.props.update({
        nickname
      }, () => {
        // router.push('/horoscope-selector');
        router.push('/today');
      });
    } else {
      this.props.update({
        nickname
      });
    }
  }

  handleChange(e, key) {
    this.setState({
      [key]: e.target.value.trim()
    });
  }

  render() {
    const { nickname } = this.state;
    const { profile } = this.props;

    return (
      <Container className="account-nickname">
        <FormItem label="昵称" labelCol="20%" labelFor="nickname">
          <input id="nickname" type="text" value={ nickname } onChange={(e) => this.handleChange(e, 'nickname')} className="input"/>
        </FormItem>
        <Button theme="green-ml" size="medium-large" text="确认" onClick={this.submit} />
      </Container>
    );
  }
});



