import * as Fetch from '../utils/fetch';
import { GET_NEWBIE_QUESTION, GET_QUESTION } from '../constants/api';

const QuestionService = {
  questionResult(session, query) {
    return Fetch.postForm(GET_QUESTION, {
      session,
      command: 1027,
      ...query
    });
  },
  newbieQuestionResult(session, query) {
    return Fetch.postForm(GET_NEWBIE_QUESTION, {
      session,
      command: 1025,
      ...query,
    })
  }
};

export default QuestionService;


