import { applyMiddleware, createStore, compose, combineReducers } from 'redux';
import createSagaMiddleware from 'redux-saga';
import { browserHistory } from 'react-router';
import { routerReducer, routerMiddleware } from 'react-router-redux';
import * as createLogger from 'redux-logger';
import { fork } from 'redux-saga/effects';

import { getReducers, getSagas } from '../utils/redux-decorators'
import rootSagas from '../sagas';
import rootReducer from '../reducers';

const reducer = combineReducers({
  ...rootReducer,
  ...getReducers(),
  routing: routerReducer
});

const sagaMiddleware = createSagaMiddleware();


const logger = createLogger({
  collapsed: true
});

/* istanbul ignore next */
const newStore = (initialState = {}) => {
  const createStoreWithMiddleware = compose(applyMiddleware(sagaMiddleware, routerMiddleware(browserHistory), logger))(createStore);
  const store = createStoreWithMiddleware(reducer, initialState);
  sagaMiddleware.run(function* () {
    yield fork(rootSagas);
    yield fork(getSagas);
  });

  if (module.hot) {
    module.hot.accept('../reducers', () => {
      store.replaceReducer(require('../reducers').default);
    });
  }

  return store;
};

export default newStore;



