/**
 * 按钮
 * size：425*138(small)/181*41(medium)/212*48(large)
 * theme: green/brown/green-long
 * @since 2017-09-28
 * @author yuan.gao@dianrong.com
 */

import * as React from 'react';

import './button.scss';

interface ButtonProps {
  onClick?: any;
  size: 'small' | 'medium' | 'medium-large' | 'large';
  theme: 'green' | 'green-ml' | 'brown' | 'green-long' | 'blue' | 'orange';
  text?: string;
}

class Button extends React.Component<ButtonProps, {}> {
  constructor(props: ButtonProps) {
    super(props);
    this.click = this.click.bind(this);
  }

  click() {
    if (this.props.onClick) {
      this.props.onClick();
    }
  }

  render() {
    const { size, theme } = this.props;
    return (
      <div className={`button ${size} ${theme}`}>
        <a onClick={this.click}>{this.props.text}</a>
      </div>
    );
  }
}

export default Button;



