import { takeEvery, put, call, fork } from 'redux-saga/effects';

import { HISTORY, SIGN_IN, SET_SHARE_CONTENT } from '../constants/actionType';
// import { SHARE_CONTENT } from '../constants/constant';
import TarotService from '../services/tarot';

export function* signIn(action) {
  try {
    const [ signIn ] = yield [
      call(TarotService.signIn, action.payload.session)
    ];

    yield put({
      type: SIGN_IN.SUCCESS,
      payload: {
        ...signIn
      }
    });
    if (action.payload.callback) {
      action.payload.callback();
    }
  } catch (err) {
    yield put({ type: SIGN_IN.FAILED });
  }
}

export function* getHistory(action) {
  try {
    const [ history ] = yield [
      call(TarotService.history, action.payload.session)
    ];

    yield [
      put({
        type: HISTORY.SUCCESS,
        payload: {
          ...history
        }
      })
    ];

    if (action.payload.callback) {
      action.payload.callback();
    }
  } catch (err) {
    yield put({ type: HISTORY.FAILED });
  }
}

export function* watchSignIn() {
  yield takeEvery(SIGN_IN.ACTION, signIn);
}

export function* watchGetHistory() {
  yield takeEvery(HISTORY.ACTION, getHistory);
}

export default function* users() {
  yield fork(watchSignIn);
  yield fork(watchGetHistory);
}



