import * as React from 'react';
import * as classNames from 'classnames';
import './formItem.scss';

export default function(props) {
  const { label, labelFor = '', className, labelCol = '30%', children, onClick } = props;
  return (
    <div className={ classNames('form-item', className) } onClick={ onClick }>
      <div className="form-item-label" style={{ width: labelCol }}>
        <label htmlFor={ labelFor }>{ label }</label>
      </div>
      <div className="form-item-wrapper" style={{ marginLeft: labelCol }}>{ children }</div>
    </div>
  );
}



