import * as React from 'react';
import Container from '../';
import Nav, { NavProps } from '../../../components/Nav';
import FormItem from '../../../components/FormItem';
import Button from '../../../components/Button';
import withUpdate from '../withUpdate';
import { validateName, validatePhone } from '../../../utils/validate';
import { toast } from '../../../components/Notification';

import './accountAddress.scss';

interface AccountAddressProps {
  profile;
  update;
}

interface AccountAddressState {
  name;
  tel;
  location;
}

export default withUpdate(class AccountAddress extends React.Component<AccountAddressProps, AccountAddressState> {
  constructor(props: AccountAddressProps) {
    super(props);
    this.state = {
      ...props.profile.address
    };

    this.submit = this.submit.bind(this);
  }
  submit() {
    const { name, tel, location } = this.state;
    const nameMsg = validateName(name, '收货人');
    if (nameMsg) {
      toast(nameMsg);
      return;
    }

    if (!location) {
      toast('请填写地址');
      return;
    }

    const telMsg = validatePhone(tel, '联系电话');
    if (telMsg) {
      toast(telMsg);
      return;
    }

    this.props.update({
      address: {
        name,
        tel,
        location
      }
    });
  }
  handleChange(e, key) {
    this.setState({
      [key]: e.target.value.trim()
    });
  }
  render() {
    const { name, tel, location } = this.state;

    return (
      <Container className="account-address">
        <FormItem label="收货人" labelCol="28%" labelFor="nickname">
          <input type="text" value={ name } onChange={(e) => this.handleChange(e, 'name')} className="input" id="nickname"/>
        </FormItem>
        <FormItem label="地址" labelCol="28%" labelFor="location">
          <textarea rows={ 3 } value={ location } onChange={(e) => this.handleChange(e, 'location')} className="input" id="location"/>
        </FormItem>
        <FormItem label="联系电话" labelCol="28%" labelFor="tel">
          <input type="text" value={ tel } onChange={(e) => this.handleChange(e, 'tel')} className="input" id="tel"/>
        </FormItem>
        <Button theme="green-ml" size="medium-large" text="确认" onClick={this.submit} />
      </Container>
    );
  }
});



