import * as React from 'react';
import * as classNames from 'classnames';

interface ContentType {
  title: string,
  content: string,
  decorate?: boolean
}
interface DetailFrameProps {
  content: Array<ContentType>;
  type?: string;
};

const DIVINE_TYPE = {
  '健康': 'health',
  '事业': 'career',
  '财运': 'wealth',
  '爱情': 'emotion',
  '人际': 'relation',
  '运势': 'fortune'
};

class DetailFrame extends React.Component<DetailFrameProps, any> {
  static defaultProps = {
    content: []
  };

  constructor(props: DetailFrameProps) {
    super(props);
  }

  render() {
    const { content, type } = this.props;

    return (
      <div className="detail-frame-wrapper">
        <div className="frame top-left" />
        <div className="frame top-right" />

        <div className={ classNames(["top-icon", DIVINE_TYPE[type]]) } />

        <div className="content">
          {
            content.map(({title, content, decorate}, index) => {
              return (
                <div key={ index }>
                  <div className={ classNames(["title", { dec: decorate }]) }>
                    { decorate && <div className="texture left" /> }
                    <h1>{ title }</h1>
                    { decorate && <div className="texture right" /> }
                  </div>
                  <div>
                    { 
                      content.split('\n').map((p, idx) => {
                        return <p key={idx}>{p}</p>;
                      }) 
                    }
                  </div>
                </div>
              );
            })
          }
        </div>

        <div className="frame bottom-left" />
        <div className="frame bottom-right" />
      </div>
    );
  }
}

export default DetailFrame;



