import * as React from 'react';
import delay from '../../../utils/delay';
const moment = require('moment');

import './todayCalendarTip.scss';

// HIDDEN: 没有挂载，MOUNTED: 挂载但是没有显示，VISIBLE: 挂载并显示
export enum todayCalendarTipStatus {
  HIDDEN,
  MOUNTED,
  VISIBLE
}

interface TodayCalendarTipProps {
  text: string;
  status: todayCalendarTipStatus;
}

interface TodayCalendarTipState {
  status: todayCalendarTipStatus;
}

export class TodayCalendarTip extends React.Component<TodayCalendarTipProps, TodayCalendarTipState> {
  constructor(props: TodayCalendarTipProps) {
    super(props);
    this.state = {
      status: todayCalendarTipStatus.HIDDEN
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.state.status) {
      this.setState({
        status: nextProps.status
      });
    }
  }

  async changeStatus(...statusList) {
    for (let i = 0; i < statusList.length; i++) {
      const status = statusList[i];
      await delay(status[0]);
      this.setState({
        status: status[1]
      });
    }
  }

  async componentDidMount() {
    this.changeStatus([0, todayCalendarTipStatus.MOUNTED]);
  }

  getClassByStatus(status) {
    return status !== todayCalendarTipStatus.HIDDEN && status !== todayCalendarTipStatus.MOUNTED ? 'visible' : '';
  }

  render() {
    const { text } = this.props;
    const { status } = this.state;
    if (status === todayCalendarTipStatus.HIDDEN) {
      return null;
    }
    const day = moment().day();
    const left = window['lib'].flexible.px2rem(160 * day - 360);
    return (
      <div className={`today-calendar-tip ${this.getClassByStatus(status)}`} style={{left}}>
        <p dangerouslySetInnerHTML={{__html: text}} />
      </div>
    );
  }
}



