import { SET_NAV_CONFIG, SET_BACKGROUND, SET_SHARE_CONTENT } from '../constants/actionType';

export function setNavConfig(config) {
  return {
    type: SET_NAV_CONFIG,
    payload: { config }
  };
}

export function setBackground(bg) {
  return {
    type: SET_BACKGROUND,
    payload: { bg }
  };
}

export function setShareContent(content) {
  return {
    type:  SET_SHARE_CONTENT,
    payload: {
      content
    }
  };
}


