import * as React from 'react';
import Avatar from '../Avatar';
import * as moment from 'moment';
import './communityItem.scss';

interface CommunityItemProps {
  user: any;
  question: string;
  questionType: string;
  questionTypeText: string;
  storyDate: string;
  cardCount: number;
  onClick?: Function;
}

class CommiunityItem extends React.Component<CommunityItemProps, {}> {
  constructor(props: CommunityItemProps) {
    super(props);
  }

  render() {
    const { user, question, questionType, questionTypeText, storyDate, cardCount } = this.props;
    return (
      <div className="community-item" onClick={ () => { if (this.props.onClick) {this.props.onClick(); } } }>
        <div className="avatar-wrap">
          <Avatar src={user.face_url} />
        </div>
        <div className="community-content">
          <p className="community-username">{user.nickname}</p>
          <time>{moment(storyDate).format('MM-DD')}</time>
          <div className="community-story-content">
            <i className={`community-story-type-${questionType}`} />
            <p>{question}</p>
          </div>
          <desc>{questionTypeText}占卜（{cardCount}张牌）</desc>
        </div>
      </div>
    );
  }
}

export default CommiunityItem;



