import { takeEvery, takeLatest, put, call, select, fork } from 'redux-saga/effects';

import {
  NEWBIE_QUESTION_GET_RESULT,
  QUESTION_GET_RESULT,
} from '../constants/actionType';
import QuestionService from '../services/question';

export function* questionResult(action) {
  const session = yield select(({ profile }) => profile.session);

  const {
    callback,
    ...query,
  } = action.payload;

  const result = yield call(QuestionService.questionResult, session, query);

  try {
    yield put({
      type: QUESTION_GET_RESULT.SUCCESS,
      payload: {
        result
      }
    });
    callback && callback(result);
  } catch(err) {
    yield put({ type: QUESTION_GET_RESULT.FAILED });
  }
}

export function* newbieQuestionResult(action) {
  const session = yield select(({ profile }) => profile.session);

  const {
    callback,
    ...query,
  } = action.payload;

  const result = yield call(QuestionService.newbieQuestionResult, session, query);

  try {
    yield put({
      type: NEWBIE_QUESTION_GET_RESULT.SUCCESS,
      payload: {
        result
      }
    });
    callback && callback(result);
  } catch(err) {
    yield put({ type: NEWBIE_QUESTION_GET_RESULT.FAILED });
  }
}

export function* watchNiewbieQuestionResult() {
  yield takeLatest(NEWBIE_QUESTION_GET_RESULT.ACTION, newbieQuestionResult);
}

export function* watchQuestionResult() {
  yield takeLatest(QUESTION_GET_RESULT.ACTION, questionResult);
}

export default function* question() {
  yield fork(watchQuestionResult);
  yield fork(watchNiewbieQuestionResult);
}



