import {
  GET_SELF_PROFILE,
  UPDATE_SELF_PROFILE,
  SET_NEW_USER_STATE
} from '../constants/actionType';

export function getSelfProfile(session, isNewPlayer, callback) {
  return {
    type: GET_SELF_PROFILE.ACTION,
    payload: {
      session,
      isNewPlayer,
      callback
    }
  };
}

export function updateSelfProfile(session, profile, callback) {
  return {
    type: UPDATE_SELF_PROFILE.ACTION,
    payload: {
      session,
      profile,
      callback
    }
  };
}

export function setIsNotNew() {
  return {
    type: SET_NEW_USER_STATE,
    payload: {
      isNew: false
    }
  };
}



