import { takeEvery, put, call, fork, takeLatest } from 'redux-saga/effects';

import { GET_SELF_PROFILE, UPDATE_SELF_PROFILE } from '../constants/actionType';
import UserService from '../services/user';

export function* getSelfProfile(action) {
  try {
    const [ selfProfile ] = yield [
      call(UserService.getSelfProfile, action.payload.session)
    ];

    yield put({
      type: GET_SELF_PROFILE.SUCCESS,
      payload: {
        ...selfProfile,
        isNew: !!parseInt(action.payload.isNewPlayer, 10),
        session: action.payload.session
      }
    });
    if (action.payload.callback) {
      action.payload.callback();
    }
  } catch (err) {
    yield put({ type: GET_SELF_PROFILE.FAILED, payload: err });
  }
}

export function* updateSelfProfile(action) {
  try {
    yield call(UserService.updateSelfProfile, action.payload.session, action.payload.profile);
    yield put({
      type: UPDATE_SELF_PROFILE.SUCCESS,
      payload: {
        ...action.payload.profile
      }
    });
    if (action.payload.callback) {
      action.payload.callback();
    }
  } catch (err) {
    yield put({ type: UPDATE_SELF_PROFILE.FAILED, payload: err });
  }
}

export function* watchGetSelfProfile() {
  yield takeEvery(GET_SELF_PROFILE.ACTION, getSelfProfile);
}

export function* watchUpdateSelfProfile() {
  yield takeLatest(UPDATE_SELF_PROFILE.ACTION, updateSelfProfile);
}

export default function* users() {
  yield fork(watchGetSelfProfile);
  yield fork(watchUpdateSelfProfile);
}



